import { createMuiTheme } from '@material-ui/core/styles';

// must have finishing slash
export const AJAX_URI = (window.location.href!=='http://localhost:3000/') ? '/server/' : 'http://altpicture.local/server/';


export const THEME = createMuiTheme({
    palette: {
        primary: {
            main:'#007bff',
        },
    }
});

export function makeGetUri(param) {

    let str = [];
    for (let key in param)
        if (param.hasOwnProperty(key)) {
            str.push(encodeURIComponent(key) + "=" + encodeURIComponent(param[key]));
        }
    let uri = str.join("&");
    uri = AJAX_URI+(uri!=='' ? '?'+uri : '');

    return uri;
}

// 1 inch ≈ 2.54 cm
// 300 dpi = 300/2.54 ≈ 118.11 dpcm (dot per cantimeter)

export function ColorCorrection(pixelArray, gain, contrast, gainR, gainG, gainB) {
    const intercept = 128 * (1 - contrast);

    for (let i = 0; i < pixelArray.data.length; i += 4) {
        pixelArray.data[i] *= gainR * gain;
        pixelArray.data[i + 1] *= gainG *gain;
        pixelArray.data[i + 2] *= gainB *gain;

        pixelArray.data[i] = pixelArray.data[i] * contrast + intercept;
        pixelArray.data[i+1] = pixelArray.data[i+1] * contrast + intercept;
        pixelArray.data[i+2] = pixelArray.data[i+2] * contrast + intercept;

        // pixelArray.data[i] = 255 * Math.pow(pixelArray.data[i] / 255, 1 / gamma);
        // pixelArray.data[i + 1] = 255 * Math.pow(pixelArray.data[i + 1] / 255, 1 / gamma);
        // pixelArray.data[i + 2] = 255* Math.pow(pixelArray.data[i + 2] / 255, 1 / gamma);
        //
        // pixelArray.data[i] = 255 * (pixelArray.data[i]/255 + offset);
        // pixelArray.data[i + 1] = 255 * (pixelArray.data[i + 1] / 255 + offset);
        // pixelArray.data[i + 2] = 255 * (pixelArray.data[i + 2] / 255 + offset);
    }
    return pixelArray;
}

export function saturate(pixelArray, amount) {
    let r,g,b;

    for (let i=0; i < pixelArray.data.length; i += 4) {
        r = (.213 + .787 * amount) * pixelArray.data[i]
            + (.715 - .715 * amount) * pixelArray.data[i + 1]
            + (.072 - .072 * amount) * pixelArray.data[i + 2];
        g = (.213 - .213 * amount) * pixelArray.data[i]
            + (.715 + .285 * amount) * pixelArray.data[i + 1]
            + (.072 - .072 * amount) * pixelArray.data[i + 2];
        b = (.213 - .213 * amount) * pixelArray.data[i]
            + (.715 - .715 * amount) * pixelArray.data[i + 1]
            + (.072 + .928 * amount) * pixelArray.data[i + 2];

        pixelArray.data[i] = r;
        pixelArray.data[i + 1] = g;
        pixelArray.data[i + 2] = b;
    }
    return pixelArray;
}

export function ColorSepia (pixelArray) {
    let r,g,b;
    for (let i=0; i < pixelArray.data.length; i += 4) {

        r = pixelArray.data[i] * 0.393 + pixelArray.data[i+1] * 0.769 + pixelArray.data[i+2] * 0.189;
        g = pixelArray.data[i] * 0.349 + pixelArray.data[i+1] * 0.686 + pixelArray.data[i+2] * 0.168;
        b = pixelArray.data[i] * 0.272 + pixelArray.data[i+1] * 0.534 + pixelArray.data[i+2] * 0.131;

        pixelArray.data[i] = r;
        pixelArray.data[i+1] = g;
        pixelArray.data[i+2] = b;
    }
    return pixelArray;
}

export function calculateImageDimensions(app, indexImage) {
    const image = app.images[indexImage];
    const realRatio = image.editorState.imageZoom*image.editorState.ratio;

    let viewportWidth = app.editorWidth - image.editorState.maskLR*2;
    let viewportHeight = app.editorHeight - image.editorState.maskTB*2;
    // let formatWidth = dpi_inch2dots(image.format.width, image.format.dpi);
    // let formatHeight = dpi_inch2dots(image.format.height, image.format.dpi);



    let sX = image.editorState.sliderPositionLR*-1;
    let sY = image.editorState.sliderPositionTB*-1;
    let sW = image.editorState.imgWidth;
    let sH = image.editorState.imgHeight;
    // console.log(' Pre-Source : ',  Math.round(sX), Math.round(sY), Math.round(sW), Math.round(sH));
    if(image.editorState.imageRotate===0 || image.editorState.imageRotate===180) {
        if(sX>0) {
            sW -= sX;
        } else if(sX<0) {
            sX=0;
        }
        if(sW+image.editorState.sliderPositionLR+sX>viewportWidth) {
            sW = viewportWidth-image.editorState.sliderPositionLR-sX;
        }
        if(sW<0) sW = 0;
        // console.log(' Pre-Source : ',  Math.round(sX), Math.round(sY), Math.round(sW), Math.round(sH));
        if(sY>0) {
            sH -= sY;
            if(sY>image.editorState.imgHeight) sY = image.editorState.imgHeight;
        } else if(sY<0) {
            sY=0;
        }
        if(sH+image.editorState.sliderPositionTB+sY>viewportHeight) {
            sH = viewportHeight-image.editorState.sliderPositionTB-sY;
        }
        if(sH<0) sH = 0;

    } else { // --------------------------------------------rotated 90 / 270
        let centerX = image.editorState.sliderPositionLR+image.editorState.imgWidth/2;
        let centerY = image.editorState.sliderPositionTB+image.editorState.imgHeight/2;

        let TB = centerY - image.editorState.imgWidth/2;
        sX = 0;
        if(TB+sW>viewportHeight) { sW = viewportHeight-TB; }
        if(TB<0) { sX=TB*-1; sW-=sX; }
        if(TB>viewportHeight){ sW = 0; }

        let LR = centerX - image.editorState.imgHeight/2;
        sY = 0;
        if(LR+sH>viewportWidth) { sY = LR+sH-viewportWidth; sH-=sY;  }
        if(LR<0) { sH += LR; }
        if(LR>viewportWidth){ sH = 0; }

    }
    if(image.editorState.imageRotate===180 || image.editorState.imageRotate===270) { // 180 --- upside // 270 --- upside
        sX = image.editorState.imgWidth - sW - sX;
        sY = image.editorState.imgHeight - sH - sY;
    }


    // console.log(' Pre-Source : ',  Math.round(sX), Math.round(sY), Math.round(sW), Math.round(sH));

    let cW = sW; //#######################################################################################
    let cH = sH; //#######################################################################################

    sX /= realRatio;
    sY /= realRatio;
    sW /= realRatio;
    sH /= realRatio;
    // console.log(' ------- Source : ',  Math.round(sX), Math.round(sY), Math.round(sW), Math.round(sH), 'dims:', Math.round(sX+sW),Math.round(sY+sH));


    let cX = image.editorState.sliderPositionLR;
    let cY = image.editorState.sliderPositionTB;
    // console.log(' ------- Pre-Canvas : ',  Math.round(cX), Math.round(cY), Math.round(cW), Math.round(cH));
    if(cX<0) {
        cX = 0;
    }
    if(cY<0) {
        cY = 0;
    }

    // console.log(' ------- Canvas : ',  Math.round(cX), Math.round(cY), Math.round(cW), Math.round(cH) );

    cX /=image.editorState.ratio;
    cY /=image.editorState.ratio;
    cW /=image.editorState.ratio;
    cH /=image.editorState.ratio;


    let pX = image.editorState.sliderPositionLR;
    let pY = image.editorState.sliderPositionTB;
    let pW = image.editorState.imgWidth;
    let pH = image.editorState.imgHeight;
    // console.log('Pre-values: ',  Math.round(pX), Math.round(pY), Math.round(pW), Math.round(pH));
    if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) {
        // let imageCenterX = pX + pW/2;
        // let imageCenterY = pY + pH/2;
        pX = (pX + pW/2) - pH/2;
        pY = (pY + pH/2) - pW/2;

        pW = image.editorState.imgHeight;
        pH = image.editorState.imgWidth;
    }
    // console.log('Pre-values: ',  Math.round(pX), Math.round(pY), Math.round(pW), Math.round(pH));
    if(pW+pX>viewportWidth) {
        pW = viewportWidth-pX;
    }
    if(pX<0) {
        pW += pX;
        pX = 0;
    }
    if(pW<0) pW = 0;

    if(pH+pY>viewportHeight) {
        pH = viewportHeight-pY;
    }
    if(pY<0) {
        pH += pY;
        pY = 0;
    }
    if(pH<0) pH = 0;


    pX /=image.editorState.ratio;
    pY /=image.editorState.ratio;
    pW /=image.editorState.ratio;
    pH /=image.editorState.ratio;

    // console.log('------Ret-values: ', Math.round(pX), Math.round(pY), Math.round(pW), Math.round(pH));



    return {
        sX: sX, sY: sY, sW: sW, sH: sH,

        cX: cX, cY: cY, cW: cW, cH: cH,

        pX: pX, pY: pY, pW: pW, pH: pH,
    };

}

export function processImage(canvas, app, index, fn_callback) {
    // this.props.parentProps.image
    const image = app.images[index];

    console.log(image);
    if(typeof fn_callback !== 'function') return;

    let img = new Image();
    img.crossOrigin = 'anonymous';

    img.onload=function(){


        if(image.editorState.imageRotate === 0 || image.editorState.imageRotate === 180) {
            canvas.width = dpi_inch2dots(image.format.width, image.format.dpi);
            canvas.height = dpi_inch2dots(image.format.height, image.format.dpi);
        } else {
            canvas.width = dpi_inch2dots(image.format.height, image.format.dpi);
            canvas.height = dpi_inch2dots(image.format.width, image.format.dpi);
        }


        const p = calculateImageDimensions(app, index);

        const ctx = canvas.getContext('2d', {alpha: false});
        ctx.fillStyle = "#ffffff";
        ctx.fillRect(0, 0, canvas.width, canvas.height);

                        console.log('processImage', p);

        if(image.editorState.imageRotate !== 0) {
            ctx.drawImage(img, p.sX, p.sX, p.sW, p.sH, p.cX, p.cY, p.cW, p.cH);
        } else {
            let zX = canvas.width/-2;
            let zY = canvas.height/-2;
            if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) {
                let t = canvas.width;
                canvas.width = canvas.height;
                canvas.height = t;
            }
            ctx.translate(canvas.width / 2, canvas.height / 2);
            ctx.rotate(image.editorState.imageRotate * Math.PI / 180);
            ctx.drawImage(img, p.sx, p.sy, p.sW, p.sH, zX + p.cX, zY + p.cY, p.cW, p.cH);

        }

        let pixelArray = ctx.getImageData(Math.floor(p.pX), Math.floor(p.pY), Math.ceil(p.pW), Math.ceil(p.pH) );

        if(image.editorState.brightness!==1 || image.editorState.contrast!==1 || image.editorState.colorR!==1 || image.editorState.colorG!==1 || image.editorState.colorB!==1 ) {
            pixelArray = ColorCorrection(pixelArray, image.editorState.brightness, image.editorState.contrast, image.editorState.colorR, image.editorState.colorG, image.editorState.colorB);
        }
        if(image.editorState.sepia) {
            pixelArray = ColorSepia(pixelArray, 1);
        }
        if(image.editorState.saturate!==1) {
            pixelArray = saturate(pixelArray, image.editorState.saturate);
        }

        ctx.putImageData(pixelArray, Math.floor(p.pX), Math.floor(p.pY));

        //const dataURL = this.myCanvas.toDataURL('image/png');
        fn_callback( canvas.toDataURL('image/jpeg', 0.7) );
    };
    img.src= makeGetUri()+image.src;

}

export function dpi_dots2cm(dots, dpi) {
    if(dpi===undefined || !dpi) dpi = 300;
    return dots / (dpi / 2.54);
}

export function dpi_cm2dots(cm, dpi) {
    if(dpi===undefined || !dpi) dpi = 300;
    return cm * (dpi / 2.54);
}

export function dpi_dots2inch(dots, dpi) {
    if(dpi===undefined || !dpi) dpi = 300;
    return dots / dpi;
}

export function dpi_inch2dots(inch, dpi) {
    if(dpi===undefined || !dpi) dpi = 300;
    return inch * dpi;
}

export function Round(x, dec) {
    if(x===undefined) x=0;
    x =  String(x).replace(/,/g,'.');
    x = parseFloat(x);
    if(isNaN(x)) x = 0;

    if(dec===undefined) {
        return x.toFixed(2);
    } else {
        return x.toFixed( parseInt(dec) );
    }
}

export function deepCompare () {
    var i, l, leftChain, rightChain;

    function compare2Objects (x, y) {
        var p;

        // remember that NaN === NaN returns false
        // and isNaN(undefined) returns true
        if (isNaN(x) && isNaN(y) && typeof x === 'number' && typeof y === 'number') {
            return true;
        }

        // Compare primitives and functions.
        // Check if both arguments link to the same object.
        // Especially useful on the step where we compare prototypes
        if (x === y) {
            return true;
        }

        // Works in case when functions are created in constructor.
        // Comparing dates is a common scenario. Another built-ins?
        // We can even handle functions passed across iframes
        if ((typeof x === 'function' && typeof y === 'function') ||
            (x instanceof Date && y instanceof Date) ||
            (x instanceof RegExp && y instanceof RegExp) ||
            (x instanceof String && y instanceof String) ||
            (x instanceof Number && y instanceof Number)) {
            return x.toString() === y.toString();
        }

        // At last checking prototypes as good as we can
        if (!(x instanceof Object && y instanceof Object)) {
            return false;
        }

        if (x.isPrototypeOf(y) || y.isPrototypeOf(x)) {
            return false;
        }

        if (x.constructor !== y.constructor) {
            return false;
        }

        if (x.prototype !== y.prototype) {
            return false;
        }

        // Check for infinitive linking loops
        if (leftChain.indexOf(x) > -1 || rightChain.indexOf(y) > -1) {
            return false;
        }

        // Quick checking of one object being a subset of another.
        for (p in y) {
            if (y.hasOwnProperty(p) !== x.hasOwnProperty(p)) {
                return false;
            }
            else if (typeof y[p] !== typeof x[p]) {
                return false;
            }
        }

        for (p in x) {
            if (y.hasOwnProperty(p) !== x.hasOwnProperty(p)) {
                return false;
            }
            else if (typeof y[p] !== typeof x[p]) {
                return false;
            }

            switch (typeof (x[p])) {
                case 'object':
                case 'function':

                    leftChain.push(x);
                    rightChain.push(y);

                    if (!compare2Objects (x[p], y[p])) {
                        return false;
                    }

                    leftChain.pop();
                    rightChain.pop();
                    break;

                default:
                    if (x[p] !== y[p]) {
                        return false;
                    }
                    break;
            }
        }

        return true;
    }

    if (arguments.length < 1) {
        return true; //Die silently? Don't know how to handle such case, please help...
        // throw "Need two or more arguments to compare";
    }

    for (i = 1, l = arguments.length; i < l; i++) {

        leftChain = [];
        rightChain = [];

        if (!compare2Objects(arguments[0], arguments[i])) {
            return false;
        }
    }

    return true;
}