import React, {PureComponent} from 'react';
import * as tools from "../../tools";
import axios from "axios";
import t from "../i18n";

import './index.css';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Icon from "@material-ui/core/Icon";
import Fab from '@material-ui/core/Fab';
import Paper from '@material-ui/core/Paper';
import LinearProgress from '@material-ui/core/LinearProgress';
// import ImageThumb from "../PageEditor/ImageThumb";

const resetState = {
    folders: [],
    tree: [],
    currentFolder: null,
    is_loadind_images: true,
    expandedImage: null,
    images: [],
    progressAjax: {},
    previewMaxResize: null,
    isAutoclearLoading: false,
    isAutoclearLoaded: 0,
};

export default class PageLoadImages extends React.Component {
    constructor(props) {
        super(props);
        this.imagesContainer = null;
        this.isAutoclearTimer = null;

        this.state = resetState;
    }


    componentDidMount() {
        // if(this.props.app.pages['loadImages']) {
        //     this.setState( this.props.app.pages['loadImages'] );
        // } else {
        //     this.onClickDir(null);
        // }
        if(this.props.app.sources[this.props.app.currentSource].autoclear) {
            this.setState({isAutoclearLoading:true}, ()=>{this.onClickDir(null);});
        } else {
            this.onClickDir(null);
        }


    }


    scrollImages(direction) {
        // console.log(this.imagesContainer);

        if(this.imagesContainer) {
            console.log('scrollTop', this.imagesContainer.scrollTop);
            console.log('scrollHeight', this.imagesContainer.scrollHeight);
            let rowH = this.imagesContainer.scrollHeight/Math.ceil((this.state.images.length/3) );
            console.log('rowCount', rowH);
            this.imagesContainer.scrollTop += rowH*direction;
        }
    }

    onClickDir(index) {
        let currentDir = (index && this.state.folders ? this.state.folders[index].dir : '' );
        let uri = '';

        if(this.state.isAutoclearLoading) {

            uri = tools.makeGetUri({'q':'source', 'id': this.props.app.currentSource, dir: currentDir});
            axios.get(uri).then(res => {
                if(res.status===200) {
                    if(res.data.error==='') {
                        this.setState({ isAutoclearLoaded: res.data.data.length } );
                    }
                }
            });

            this.isAutoclearTimer = setTimeout( ()=>{this.onClickDir()}, 3000);

        } else { // ------------------------------------------- full load folders and images

            if(this.isAutoclearTimer) clearTimeout(this.isAutoclearTimer);

            let _d = '';
            let folders = [{
                'list': this.props.app.currentSource,
                'source': this.props.app.sources[this.props.app.currentSource],
                'name': this.props.app.sources[this.props.app.currentSource].name,
                'dir': _d,
                'level': 0,
            }];
            this.setState({folders: folders, currentFolder: null });

            uri = tools.makeGetUri({'q':'folders', 'id': this.props.app.currentSource, dir: currentDir });
            axios.get(uri).then(res => {
                if(res.status===200) {
                    if(res.data.error==='') {
                        var _L = 1;
                        for (let i=0; i<res.data.tree.length; i++) {
                            _d += (_d!=='' ? '/' : '')+res.data.tree[i];
                            let _f = {
                                'list': this.props.app.currentSource,
                                'name': res.data.tree[i],
                                'dir': _d,
                                'level': _L,
                            };
                            if(_L<5) _L++;
                            folders.push(_f);
                        }

                        for (let _f of res.data.data) {
                            _f.level = _L;
                            folders.push(_f);
                        }
                        this.setState({
                            folders: folders,
                            currentFolder: res.data.tree.length,
                        });

                        this.setState({images: [], is_loadind_images: true });

                        let progress = 3;
                        this.props.appSetState({ progressBarValue:progress });
                        let uri = tools.makeGetUri({'q':'source', 'precache':true, 'id': this.props.app.currentSource, dir: currentDir});
                        axios.get(uri, {
                            onDownloadProgress: (progressEvent) => {
                                let percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
                                progress = progressEvent.lengthComputable ? percentCompleted : progress;
                                // console.log(progress);
                                this.props.appSetState({ progressBarValue: progress })
                            }
                        })
                            .then((/*response*/) => {
                                this.props.appSetState({ progressBarValue:-1 });

                                uri = tools.makeGetUri({'q':'source', 'id': this.props.app.currentSource, dir: currentDir});
                                axios.get(uri).then(res => {
                                    if(res.status===200) {
                                        if(res.data.error==='') {
                                            this.setState({ images: res.data.data }, ()=>{this.imagesScroll()} );
                                        }
                                    }
                                    this.setState({is_loadind_images: false});
                                });


                            }).catch(error => {
                            if (error.response) {
                                console.log(error.responderEnd);
                                this.props.onMessage('testProgress error: '+error.responderEnd);
                                progress = -1;
                                this.props.appSetState({ progressBarValue:-1 })
                            }
                        });

                    }
                }
            });

        }


    }


    showExpandImg(index) {
        this.setState({expandedImage: null} , ()=>{
            this.setState({expandedImage: index}, ()=>{this.reopenThumb(index)})
        })
    }
    reopenThumb(index) {
        //image.name
        let newIm = this.state.images.slice();
        let reopen = newIm[index].name;
        newIm[index].name = '';
        this.setState({images: newIm}, ()=>{
            newIm[index].name = reopen;
            this.setState({images: newIm});
        });

    }

    hideExpandImg() {
        this.setState({expandedImage: null})
    }
    MarkImage(index){
        let newIm = this.state.images.slice();
        newIm[index].marked = !newIm[index].marked;
        this.setState({images: newIm});
    }

    markAllImages(mode) {
        let newIm = this.state.images;
        for(let i=0; i<newIm.length; i++) {
            newIm[i].marked = mode;
        }
        this.setState({images: newIm});
    }


    setpreviewMaxResize(value) {
        this.setState({previewMaxResize:value});
    }

    nextPage = ()=> {

        // args - this.state.images
        const selectedimages = this.state.images.filter(function(x){return x.marked});
        if(selectedimages.length===0) {
            this.props.onMessage(t('To continue you should select at least one image'));
            return;
        }
        const dirs = selectedimages.map( (image)=> { return image.dir;} );
        const files = selectedimages.map( (image)=> { return image.name;} );

        let uri = tools.makeGetUri();
        const params = new URLSearchParams();
        params.append('q', 'movetowork');
        params.append('id', this.props.app.currentSource);
        params.append('maxWidth', (this.state.previewMaxResize ? this.state.previewMaxResize : 300));

        for(let _d of dirs ) params.append('dirs[]', _d);
        for(let _f of files ) params.append('files[]', _f);


            let progress = 3;
            this.props.appSetState({ progressBarValue: progress, isPleaseWait:true });
            axios.post(uri, params, {
                onDownloadProgress: (progressEvent) => {
                    let percentCompleted = Math.round((progressEvent.loaded * 100) / progressEvent.total);
                    progress = progressEvent.lengthComputable ? percentCompleted : progress;
                    // console.log(progress);
                    this.props.appSetState({ progressBarValue: progress })
                }
            })
                .then((/*response*/) => {
                    progress = -1;
                    this.props.appSetState({ progressBarValue:-1 });

                    let uri = tools.makeGetUri({'q':'workpath'});
                    axios.get(uri).then(res => {
                        if(res.status===200) {
                            if(res.data.error==='') {
                                let dpi =  this.props.app.formats[this.props.app.currentFormat].dpi ? this.props.app.formats[this.props.app.currentFormat].dpi : 300;
                                const images = selectedimages.map( (image)=> {
                                    let thumbname = res.data.data.thumbs+image.dir+ image.name.substr(0, image.name.lastIndexOf(".")) +".jpg";
                                    return {
                                        name: image.name,
                                        extension: image.extension,
                                        width: tools.dpi_dots2inch(image.width, dpi),
                                        height: tools.dpi_dots2inch(image.height, dpi),
                                        width_pixel: image.width,
                                        height_pixel: image.height,
                                        orientation : image.orientation,
                                        thumb: thumbname.replace('?', '%3F'),
                                        src: res.data.data.works+image.name,
                                        format: this.props.app.formats[this.props.app.currentFormat],
                                        count: 1,
                                        editorState: {

                                            maskTB: 0,
                                            maskLR: 0,
                                            imageZoom: 1.0,
                                            imageZoomMax: 1.9,
                                            imageZoomMin: 0.1,
                                            imageRotate: 0,
                                            colorR: 1.0,
                                            colorG: 1.0,
                                            colorB: 1.0,
                                            brightness: 1,
                                            contrast: 1,
                                            saturate: 1,
                                            sepia: false,

                                            sliderPositionTB: 0,
                                            sliderPositionLR: 0,

                                            initedImagePosition: false,
                                            initedMask: false,
                                            ratio: 1.0,
                                            imgWidth: image.width,
                                            imgHeight: image.height,
                                            currentTab: 0,

                                        },


                                };
                                } );
                                this.props.onClickNextBtn(images);

                            }
                        }
                    });

                    //console.log(images);
                    // this.setState({appstate: 'editor', images: images});
                }).catch(error => {
                    if (error.response) {
                        console.log('movetowork error', error.responderEnd);
                        this.props.onMessage('movetowork error: '+error.responderEnd);
                        progress = -1;
                        this.props.appSetState({ progressBarValue:-1, isPleaseWait:false })
                    }
                });

    };

    imagesScroll() {
        // console.log(this.imagesContainer.children);
        let images = this.state.images.slice();
        let changed = false;
        if(this.imagesContainer && this.imagesContainer.children) {
            let viewHeight = this.imagesContainer.clientHeight;
            let len = this.imagesContainer.children.length;
            for(let i=0; i<len; i++) {
                if(images[i] && !images[i].is_appeared) {
                    let rect = this.imagesContainer.children[i].getBoundingClientRect();
                    let is_visible = !(rect.bottom < 0 || rect.top - viewHeight >= 0);
                    if(is_visible) {
                        images[i].is_appeared = true;
                        changed = true;
                    }
                }
            }
        }
        if(changed) {
            this.setState({images:images});
        }
    }



    render() {

        const selectedimages = this.state.images.filter(function(x){return x.marked});
        const cost = this.props.app.formats[this.props.app.currentFormat].cost;

        return (

            <div className="app-page app-bg-e0 app-page-start w-100 h-100">
                <div className="app-main h-90">
                    <div className="d-inline-block w-85 h-100">
                        <div className="app-main-selectors w-100 h-100 d-flex flex-row">
                            <div  className="app-main-selectors-folder h-100 p-2 flex-grow-1 flex-shrink-1">
                                <Paper className="scrollbar p-2 h-100 app-bg-e">
                                    {this.state.folders.map( (dir, index)=>
                                        <Card key={index}
                                              className={'m-1 app-dir-item level'+dir.level+' '+(this.state.currentFolder===index ? ' text-white bg-primary' : '')}
                                              onClick={() => this.onClickDir(index) }
                                        >
                                            <CardContent>
                                                {dir.source && (
                                                    <img src={tools.makeGetUri()+dir.source.icon} alt={dir.source.name} className="app-dir-item-icon"/>
                                                )}
                                                {dir.name}
                                                {index===0 &&
                                                <Icon className="float-right" >refresh</Icon>
                                                }
                                            </CardContent>
                                        </Card>
                                    )}
                                </Paper>
                            </div>
                            <div className="app-main-selectors-image h-100 p-2 flew-grow-0 flex-shrink-0">
                                <Paper className="h-100 position-relative app-bg-e">
                                    <div  ref={(node)=>{this.imagesContainer=node}}
                                          onScroll={()=>this.imagesScroll()}
                                          className="p-2 h-100 scrollbar">
                                            {this.state.images.map( (image, index)=>
                                                <ImagePreview
                                                    key={image.name}
                                                    image={image}
                                                    itemIndex={index}
                                                    imagesContainer = {this.imagesContainer}
                                                    onExpand={()=>this.showExpandImg(index)}
                                                    onMark={()=>this.MarkImage(index)}
                                                    setpreviewMaxResize={()=>this.setpreviewMaxResize.bind(this)}
                                                    {...this.props}
                                                />
                                            )}

                                    </div>

                                    {(!this.state.is_loadind_images && this.state.images.length===0) && (
                                        <div className='app-source-noimages h-100 text-primary' >
                                            {t('No images')}
                                        </div>
                                    )}

                                    {(this.state.is_loadind_images && !this.state.isAutoclearLoading) && (
                                        <div className = "list-images-loader spinner-border text-primary" role = "status" >
                                            <span className = "sr-only" > Loading...</span>
                                        </div>

                                    )}

                                </Paper>
                            </div>

                        </div>

                    </div>
                    <div className="app-main-rightwidget w-15 h-100 p-2 d-inline-block">
                        <Paper className="h-100 p-2 app-bg-e position-relative">
                            <div className="_fabwrapper">
                                <button className={"btn btn-big btn-primary"}
                                        onClick={() => this.nextPage()}
                                        disabled={false}
                                        title={t("Next")}
                                        type="button" >
                                    <Icon>arrow_forward</Icon> {t('Next')}
                                </button>
                            </div>

                            <div className="_fabwrapper">
                                <button className={"btn btn-big btn-primary"}
                                        onClick={() => {this.props.onClickPrevBtn()}}
                                        disabled={false}
                                        title={t("Previous")}
                                        type="button" >
                                    <Icon>arrow_back</Icon> {t("Previous")}
                                </button>
                            </div>


                            <div className="_fabwrapper mt-4">
                                <button className={"btn btn-success"}
                                        onClick={()=>this.scrollImages(-1)}
                                        disabled={false}
                                        title={t("up")}
                                        type="button" >
                                    <Icon>expand_less</Icon>
                                </button>
                            </div>

                            <div className="_fabwrapper">
                                <button className={"btn btn-success mr-2"}
                                        onClick={() => this.markAllImages(true)}
                                        disabled={false}
                                        title={t("Select all")}
                                        type="button" >
                                    <Icon>filter</Icon>
                                </button>

                                <button className={"btn btn-success"}
                                        onClick={() => this.markAllImages(false)}
                                        disabled={false}
                                        title={t("Select none")}
                                        type="button" >
                                    <Icon>filter_none</Icon>
                                </button>
                            </div>

                            <div className="_fabwrapper">
                                <button className={"btn btn-success"}
                                        onClick={()=>this.scrollImages(1)}
                                        disabled={false}
                                        title={t("down")}
                                        type="button" >
                                    <Icon>expand_more</Icon>
                                </button>
                            </div>


                            <div className="app-main-rightwidget-tablewrp">
                                <table className="table">
                                    <tbody>
                                    <tr>
                                        <th scope="row">{t('Files')}</th>
                                        <th scope="row">{this.state.images.length}</th>
                                    </tr>
                                    <tr>
                                        <th scope="row">{t('Selected')}</th>
                                        <th scope="row">{selectedimages.length}</th>
                                    </tr>
                                    <tr>
                                        <th scope="row">{t('Cost')}</th>
                                        <th scope="row">{tools.Round(selectedimages.length*cost)}</th>
                                    </tr>
                                    </tbody>
                                </table>

                            </div>

                        </Paper>

                    </div>

                </div>

                <div className="app-bottomframe w-100 h-10 p-2 position-relative">
                    <Paper className="p-2 app-bg-e h-100 w-100 app-bottom-msg-wrp">
                        <Icon className="_icon text-white">info</Icon>
                        <div className="_msg">
                            {t('Select folder from list, and choose pictures to print. To view in fullscreen click on the name of picture')}
                        </div>
                    </Paper>

                    {(this.props.app.progressBarValue>=0 && (
                    <div className="app-bottom-progress h-100 p-2 w-100">
                        <LinearProgress variant="determinate" value={this.props.app.progressBarValue} />
                    </div>
                    ))}
                </div>

                {this.state.isAutoclearLoading && (
                    <div className="app-please-wait-wrp position-fixed">

                        <Paper className="app-page-loadimages-wait p-3">
                            <div className="app-source-icon-wrp">
                                <img src={tools.makeGetUri()+this.props.app.sources[this.props.app.currentSource].icon} alt={this.props.app.sources[this.props.app.currentSource].name} className="app-source-icon"/>
                            </div>
                            {this.props.app.sources[this.props.app.currentSource].name}
                            <div className="_loaded p-3">
                                {t('Loaded already')}: {this.state.isAutoclearLoaded}
                            </div>

                            <div className="_fabwrapper">
                                <button className={"btn btn-big btn-success"}
                                        onClick={()=>{this.setState({isAutoclearLoading:false}, ()=>{this.onClickDir()}) }}
                                        disabled={false}
                                        title={t("OK")}
                                        type="button" >
                                    {t('Next')}
                                </button>
                            </div>

                        </Paper>

                    </div>
                )}


                <ModalExpand
                    image={this.state.expandedImage!==null ? this.state.images[this.state.expandedImage] : null}
                    itemCurrent={this.state.expandedImage}
                    itemPrev={this.state.expandedImage>0 ? this.state.expandedImage-1: null}
                    itemNext={this.state.expandedImage<(this.state.images.length-2) ? this.state.expandedImage+1 : null}
                    onExpand={this.showExpandImg.bind(this)}
                    onMark={this.MarkImage.bind(this)}
                    onClose={this.hideExpandImg.bind(this)}
                />
            </div>
        );
    }
}

class ImagePreview extends PureComponent {

    render() {
        const image = this.props.image;
        // this.props.image.orientation
        // 1 - no rotate
        // 3 - 180deg
        // 6 - 270deg
        // 8 - 90deg
        var rotate = "";
        switch (this.props.image.orientation) {
            case 3:
                rotate = " rotate180";
                break;
            case 6:
                rotate = " rotate90";
                break;
            case 8:
                rotate = " rotate270";
                break;
            default:
                break;
        }

        let previewMaxResize = 300;
        if(this.props.previewMaxResize) {
            previewMaxResize = this.props.previewMaxResize;
        } else {
            if(this.imgWrapper) {
                previewMaxResize = this.imgWrapper.clientWidth;
                this.props.setpreviewMaxResize(previewMaxResize);
            }
        }

        let imgStyle = {backgroundImage: 'url('+tools.makeGetUri({'q':'img', 'id':image.list, dir:image.dir, 'src':image.name, 'max':previewMaxResize})+')'};

        return (
            <Card className='app-source-image-item d-inline-block m-1 position-relative' >
                <CardContent className={"p-0"}
                     onClick={()=>this.props.onMark()}
                     onDoubleClick={()=>this.props.onExpand(this.props.itemIndex)}
                >
                    <div ref={(node)=>{this.imgWrapper=node}}
                        className={"app-source-image-item-img-wrapper p-1 "+(image.marked ? ' text-white bg-primary': '')}>
                        {image.is_appeared &&
                        <div className={"app-source-image-item-img border "+rotate} style={imgStyle} />
                        }
                        {!image.is_appeared &&
                        <div className="app-source-image-item-img border " >
                        </div>
                        }

                        <div className='app-source-image-item-toggle position-absolute'>
                            <Fab className="bg-primary text-light"
                                onClick={(e)=> {e.stopPropagation(); this.props.onMark()}}
                                aria-label="Check" >
                                <Icon>check</Icon>
                            </Fab>
                        </div>
                        <div className='app-source-image-item-fullscreen position-absolute'>
                            <Fab className="bg-success text-light"
                                onClick={()=>this.props.onExpand(this.props.itemIndex)}
                                aria-label="Fullscreen" >
                                <Icon>zoom_in</Icon>
                            </Fab>
                        </div>


                        <div className="app-selectimages-item-text text-center m-1">
                            {image.name}
                        </div>

                    </div>
                </CardContent>
            </Card>
        )
    }
}

class ModalExpand extends PureComponent {

    render() {
        const image = this.props.image;

        if (image===null) return null;

        var rotate = "";
        switch (image.orientation) {
            case 3:
                rotate = " rotate180";
                break;
            case 6:
                rotate = " rotate90";
                break;
            case 8:
                rotate = " rotate270";
                break;
            default:
                break;
        }

        let imgsrc = tools.makeGetUri({'q':'img', 'id':image.list, dir:image.dir, 'src':image.name, 'max':-1});

        return (
            <div className="app-modal" onClick={()=>this.props.onClose()}>
                <div className="app-modal-main position-fixed">
                        <div className={"rotate-wrp app-source-image-expanded rounded"+rotate+(image.marked ? ' bg-primary': '')}>
                            <img src={imgsrc} alt="zoomed" onClick={(e)=> {e.stopPropagation(); this.props.onMark(this.props.itemCurrent)}} />
                        </div>

                </div>

                <div
                    onClick={(e)=>{if(this.props.itemPrev===null)return; e.stopPropagation(); this.props.onExpand(this.props.itemPrev)}}
                    className='app-image-expanded-button _prev position-absolute'>
                    <Fab disabled={this.props.itemPrev===null} aria-label="Previous">
                        <Icon>navigate_before</Icon>
                    </Fab>
                </div>

                <div
                    onClick={(e)=> {e.stopPropagation(); this.props.onMark(this.props.itemCurrent)}}
                    className='app-image-expanded-button _check position-absolute'>
                    <Fab
                        aria-label="Check" >
                        <Icon className="text-success">check</Icon>
                    </Fab>
                </div>

                <div
                    onClick={(e)=>{if(this.props.itemNext===null)return; e.stopPropagation(); this.props.onExpand(this.props.itemNext)}}
                    className='app-image-expanded-button _next position-absolute'>
                    <Fab disabled={this.props.itemNext===null}
                         aria-label="Next">
                        <Icon>navigate_next</Icon>
                    </Fab>
                </div>

                <div
                    onClick={(e)=>{e.stopPropagation(); this.props.onClose()}}
                    className='app-image-expanded-button _close position-absolute'>
                    <Fab aria-label="Close" >
                        <Icon>close</Icon>
                    </Fab>
                </div>

            </div>
        );


    }

}
