import React from 'react';
import './App.css';
import {version} from './version.json';

import {makeGetUri, THEME} from './tools.js';
import axios from "axios";
import 'bootstrap/dist/css/bootstrap.css';
import PageStart from './Components/PageStart';
import PageLoadImages from './Components/PageLoadImages';
import PageEditor from './Components/PageEditor';
import PageSetup from './Components/PageSetup';
import PagePrint from './Components/PagePrint';
import SetupPin from './Components/SetupPin.js';

import LoaderSplashScreen from './Components/LoaderSplashScreen';

import CssBaseline from '@material-ui/core/CssBaseline';
import Snackbar from '@material-ui/core/Snackbar';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

import { MuiThemeProvider } from '@material-ui/core/styles';
import './assets/iconfont/material-icons.css';
import './assets/iconfont/test.css';

import * as tools from "./tools";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import t from "./Components/i18n";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";


// CANCELED: запрос на відновлення роботи

// DONE: repeat apply contrast & brightness & color from previous photo (and undo)

// TODO: поворот вікна едітора - портрет/ландскейп
// DONE: при створенні нового формата порожнє поле DPI
// TODO: printMode 'backend - Wait for each file' broken
// TODO: перевірити роботу printMode frontent
// TODO: перегляд виконаної роботи після друку
// TODO: під лінуксом CDROM постійно норовить закритися, і пропадає/появляється з меню


class App extends React.Component {
    constructor(props) {
        super(props);
        this.updateSavePageTimer = null;
        this.appWrapperNode = null;
    }
    state = {
        appstate: 'start',
        pages: {},
        sources: [],
        currentSource: null,
        formats: [],
        currentFormat: null,
        images: [],
        is_loading_init: false,

        editorHeight: 0,
        editorWidth: 0,

        // needUpdateCanvas: false,

        snackMessage: null,
        setup: {
            batchSaveMode: false
        },

        isAppStarting: true,
        isSetup: false,
        isSetupLogged: false,
        isSetupPassword: '',
        isUploadEnable: false,
        isPleaseWait: false,
        progressBarValue: -1,

        isDebugShow: false,

        appWidth: null,
        appHeight: null,

        serverError: null,
        version: version
    };


    appSetState = (stateToSave, fn_callback)=> {
        let state = this.state;
        for (let prop in stateToSave) {
            state[prop] = stateToSave[prop];
        }
        this.setState(state,
            ()=>{
                if( typeof(fn_callback) === 'function') fn_callback();
            });
    };

    appImageChanged = (index, editorState, fn_callback) => {
        let image = this.state.images[index];
        for (let prop in editorState) {
            image.editorState[prop] = editorState[prop];
        }
        let images = this.state.images;
        images[index] = image;
        this.setState({
            images: images,
        }, ()=>{
            if( typeof(fn_callback) === 'function') fn_callback();
        })
    };


    appEditorChanged = (clientWidth,clientHeight) => {
        // console.log("appEditorChanged: "+this.state.editorWidth+" === "+clientWidth+" && "+this.state.editorHeight+" === "+clientHeight)

        if(this.state.editorWidth === clientWidth && this.state.editorHeight === clientHeight) {
            return;
        }

        let images = this.state.images;
        let len = images.length;
        for(let i=0; i<len; i++ ) {
            images[i].editorState.initedMask = false;
        }
        this.setState({
            editorWidth: clientWidth,
            editorHeight: clientHeight,
            images: images,
        }, ()=>{
            if(this.state.currentImage>=0) {
                console.log('try reinit');
                this.appImageInitMask(this.state.currentImage, true);
            }
        })
    };

    appImageInitMask(index, force, fn_callback) {

        let image = this.state.images[index];
        if(image.editorState.initedMask && !force) {
            if( typeof(fn_callback) === 'function') fn_callback();
            return;
        }

        // console.log(p.stateEditorWidth, p.editorWidth, p.stateEditorHeight, p.editorHeight);
        let formatRatio = (image.format.height && image.format.width ? image.format.height / image.format.width : 1);
        // console.log('this ratio : '+ratio);
        let maskTB = 0;
        let maskLR = 0;

        let editorRatio = this.state.editorHeight / this.state.editorWidth;
        // console.log('editorWidth:'+this.state.editorWidth, ' editorHeight:'+this.state.editorHeight);
        if(formatRatio>editorRatio) {
            maskLR = (this.state.editorWidth-image.format.width/image.format.height*this.state.editorHeight)/2;
        } else {
            maskTB = (this.state.editorHeight-image.format.height/image.format.width*this.state.editorWidth)/2;
        }

        let newState = {
            maskTB: maskTB,
            maskLR: maskLR,
            ratio: (this.state.editorHeight-maskTB*2)/tools.dpi_inch2dots(image.format.height, image.format.dpi),
            initedMask: true,
        };
        // console.log(newState);
        // if(!ret) {
            this.appImageChanged(index, newState, ()=>{
                if( typeof(fn_callback) === 'function') fn_callback();
            });
        // }

        // return newState;

    }

    alignInit = (index, fn_callback)=> {
        let image = this.state.images[index];
        let format = image.format;
        let initrotate = false;
        let format_lanscape = (format.width-format.height)>1;
        let image_landscape = (image.width-image.height)>1;

        // console.log('format_lanscape:', format.width,'>', format.height, '=', format_lanscape);

        if(format_lanscape !== image_landscape) {
            initrotate = true;
                        // console.log('initrotate', initrotate);
        }

        if(initrotate) {
            if(format_lanscape) {
                this.alignPortrait(index, fn_callback,true, initrotate);
            } else {
                this.alignLandscape(index, fn_callback, true, initrotate);
            }
        } else {
            if(format_lanscape) {
                this.alignLandscape(index, fn_callback, true);
            } else {
                this.alignPortrait(index, fn_callback, true);
            }
        }
    };

    alignLandscape = (index, fn_callback, init, initrotate)=> {
        let image = this.state.images[index];
                                // console.log('alignLandscape landscape', image.editorState.ratio);
        let sliderPositionLR = 0;
        let imageZoom = image.format.width/image.width;
        let imgWidth = tools.dpi_inch2dots(image.width, image.format.dpi) * imageZoom * image.editorState.ratio;
        let imgHeight = tools.dpi_inch2dots(image.height, image.format.dpi) * imageZoom * image.editorState.ratio;
        let viewportY = this.state.editorHeight - image.editorState.maskTB*2;
        let sliderPositionTB = (viewportY - imgHeight)/2;
        let isWhiteFields = viewportY > imgHeight;
                                // console.log('alignLandscape', 'isWhiteFields', isWhiteFields,)

        if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270 || initrotate) {
            imageZoom = image.format.width/image.height;
            imgWidth = tools.dpi_inch2dots(image.width, image.format.dpi) * imageZoom * image.editorState.ratio;
            imgHeight = tools.dpi_inch2dots(image.height, image.format.dpi) * imageZoom * image.editorState.ratio;
            sliderPositionTB = (imgWidth-imgHeight)/2 + (viewportY-imgWidth)/2 ;
            sliderPositionLR = (imgHeight-imgWidth)/2;
            isWhiteFields = viewportY > imgWidth;
                                // console.log('alignLandscape', 'isWhiteFields', isWhiteFields,)
        }

        if(init && isWhiteFields) {
            this.alignPortrait(index, fn_callback, false, initrotate);
            return;
        }

        let editorState = {
            imageZoom : imageZoom,
            imageZoomMax: imageZoom*5,
            imageZoomMin: imageZoom*0.5,
            sliderPositionTB: sliderPositionTB,
            sliderPositionLR: sliderPositionLR,
            imgWidth: imgWidth,
            imgHeight: imgHeight,
            initedImagePosition: !(imgWidth===0 && imgHeight===0),
            needUpdateCanvas: imageZoom>image.editorState.imageZoom,
        };
        if(initrotate) editorState['imageRotate']=90;

        this.appImageChanged(index, editorState, fn_callback);

    };
    alignPortrait = (index, fn_callback, init, initrotate)=> {
        let image = this.state.images[index];
                                // console.log('alignPortrait', image.editorState.ratio);
        let sliderPositionTB = 0;
        let imageZoom = image.format.height/image.height;
        let imgWidth = tools.dpi_inch2dots(image.width, image.format.dpi) * imageZoom * image.editorState.ratio;
        let imgHeight = tools.dpi_inch2dots(image.height, image.format.dpi) * imageZoom * image.editorState.ratio;
        let viewportX = this.state.editorWidth - image.editorState.maskLR*2;
        let sliderPositionLR = (viewportX - imgWidth)/2;
        let isWhiteFields = viewportX > imgWidth;
                                // console.log('alignPortrait', 'isWhiteFields', isWhiteFields,)

        if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270 || initrotate) {
            imageZoom = image.format.height/image.width;
            imgWidth = tools.dpi_inch2dots(image.width, image.format.dpi) * imageZoom * image.editorState.ratio;
            imgHeight = tools.dpi_inch2dots(image.height, image.format.dpi) * imageZoom * image.editorState.ratio;
            sliderPositionLR = (imgHeight-imgWidth)/2 + (viewportX-imgHeight)/2 ;
            sliderPositionTB = (imgWidth-imgHeight)/2;
            isWhiteFields = viewportX > imgHeight;
                                // console.log('alignPortrait', 'isWhiteFields', isWhiteFields,)
        }

        if(init && isWhiteFields) {
            this.alignLandscape(index, fn_callback, false, initrotate);
            return;
        }

        let editorState = {
            imageZoom: imageZoom,
            imageZoomMax: imageZoom*5,
            imageZoomMin: imageZoom*0.5,
            sliderPositionTB: sliderPositionTB,
            sliderPositionLR: sliderPositionLR,
            imgWidth: imgWidth,
            imgHeight: imgHeight,
            initedImagePosition: !(imgWidth===0 && imgHeight===0),
            needUpdateCanvas: imageZoom>image.editorState.imageZoom,
        };
        if(initrotate) editorState['imageRotate']=90;

        this.appImageChanged(index, editorState, fn_callback);

    };

    updateDimensions() {
        if(this.appWrapperNode) {
            let appWidth = this.appWrapperNode.clientWidth;
            let appHeight = this.appWrapperNode.clientHeight;
            if(this.appWrapperNode.clientWidth > this.appWrapperNode.clientHeight) {
                // console.log(appWidth,appHeight);
                this.setState({appWidth: appWidth, appHeight: appHeight});
            }
        }
    }

    checkLoadedState = (state)=> {
        state.isUploadEnable = false;
        delete state.version;
        delete state.appWidth;
        delete state.appHeight;
        state.isAppStarting = false;
        return state;
    };

    componentWillUnmount() {
        window.removeEventListener("resize", this.updateDimensions);
    }
    componentDidMount() {
        window.addEventListener("resize", this.updateDimensions);

        // this.updateDimensions();

        let uri = makeGetUri({'q':'init'});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    if(res.data.data) {
                        let state = this.checkLoadedState(res.data.data);
                        for (let prop in res.data.setup) {
                            state.setup[prop] = res.data.setup[prop];
                        }
                        this.setState(state, ()=>{
                            this.updateDimensions();
                        });
                    }
                } else {
                    this.setState({serverError:res.data.error});
                }
            } else {
                this.setState({serverError:'Error: server fail'});
            }
        }).catch((error)=>{
            console.log(error);
            this.setState({serverError:error.message});
        });
    }

    resetApp() {
        let uri = makeGetUri({'q':'init', 'reset':'reset'});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    if(res.data.data) {
                        let state = this.checkLoadedState(res.data.data);
                        for (let prop in res.data.setup) {
                            state.setup[prop] = res.data.setup[prop];
                        }
                        this.setState(state, ()=>{ this.updateDimensions(); this.savePages(); });
                    }
                }
            }
        });

    }



    onMessage = (msg) => {
      if(msg) {
          this.setState({
              snackMessage: msg
          })
      }
    };
    onMessageHide = () => {
        this.setState({
            snackMessage: null
        })
    };

    uploadEnable =(ev)=> {
        ev.preventDefault();

        let uri = tools.makeGetUri({'q':'login', 'pass': this.state.isSetupPassword});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    this.setState({isUploadEnable:true, isSetup:false});
                } else {
                    console.log(res.data.error);
                    this.Logout();
                }
            }
        });

    };

    Login = (ev) => {
        ev.preventDefault();
        // ev.stopPropagation();
        let uri = tools.makeGetUri({'q':'login', 'pass': this.state.isSetupPassword});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    this.setState({isSetupLogged: true,})
                } else {
                    console.log(res.data.error);
                    this.Logout();
                }
            }
        });
        return false;
    };

    Logout = () => {
        this.setState({isSetup : false, isSetupLogged: false, isSetupPassword: '' }, ()=>{this.savePages();});

    };



    savePages(pageid, pagestate, OKmessage) {
      let newAppState = this.state;

        if(this.updateSavePageTimer) clearTimeout(this.updateSavePageTimer);
        this.updateSavePageTimer = setTimeout(()=>{
            let uri = makeGetUri();
            const params = new URLSearchParams();
            params.append('q', 'saveClientState');
            params.append('state', JSON.stringify(newAppState));
            axios.post(uri, params ).then(res => {
                if(res.status===200) {
                    if(res.data.error==='') {
                        if(OKmessage) this.onMessage(OKmessage);
                        // console.log('state saved, '+this.state.appstate);
                    }
                }
            });
        }, 1700);
    }


    sourceClick(id) {
      //console.log(id);
        const source = this.state.sources[id];
        if(!source.enabled) return;

        if(!source.emptyhide || (source.emptyhide && source.hasFiles))
            this.setState({currentSource: id})
    }

    formatClick(id) {
        //console.log(id);
        this.setState({currentFormat: id})
    }

    removeTmp( fn_callback ){
        let uri = makeGetUri({'q':'removetmp'});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    if( typeof(fn_callback) === 'function') fn_callback();
                }
            }
        });
    }

    scriptSource = (source, s_key) => {
        // let s_key = 'scripton';
        if(source[s_key] && source[s_key]!=='') {
            let uri = makeGetUri({'q':'scriptSource', 'scripts':source[s_key]});
            axios.get(uri);
        }
    };

    autoclearSource = (source_id) => {
        if(this.state.sources[source_id].autoclear) {
            let uri = makeGetUri({'q':'autoclearSource','id':source_id} );
            axios.get(uri);
        }
    };

    ClickNextBtn(args) {
      switch (this.state.appstate) {
          case 'start':
              this.scriptSource(this.state.sources[this.state.currentSource], 'scripton');
              this.removeTmp(()=>{ this.setState({appstate: 'loadImages'}) });
              break; // ---------------------------------------------------------------------------------

          case 'loadImages':
              this.scriptSource(this.state.sources[this.state.currentSource], 'scriptoff');
              this.setState({appstate:'editor', images:args, currentImage:null, isPleaseWait:false });
              break; // ---------------------------------------------------------------------------------

          case 'editor':
              this.setState({appstate:'print' }, ()=>{this.savePages()});
              break; // ---------------------------------------------------------------------------------

          case 'print':
              this.autoclearSource(this.state.currentSource);
              this.resetApp();
              break; // ---------------------------------------------------------------------------------

          default:
              alert('no action');
              break;
      }
    }

    ClickPrevBtn() {
        switch (this.state.appstate) {
            case 'loadImages':
                this.autoclearSource(this.state.currentSource);
                this.resetApp();
                break; // ---------------------------------------------------------------------------------

            case 'editor':
                this.scriptSource(this.state.sources[this.state.currentSource], 'scripton');
                this.setState({appstate: 'loadImages'});
                break; // ---------------------------------------------------------------------------------

            case 'print':
                this.setState({appstate: 'editor'});
                break; // ---------------------------------------------------------------------------------

            default:
                alert('no action');

        }
    }

    render() {

        let wrongOrientation = (!this.state.serverError && this.appWrapperNode && this.appWrapperNode.clientHeight > this.appWrapperNode.clientWidth) ? true: false;

        let toRenderd = <div> </div>;
        let appstate = this.state.appstate;
        if(this.state.isSetupLogged) {
            appstate = 'setup';
        }

        if(this.state.isAppStarting || wrongOrientation) {
            toRenderd = <LoaderSplashScreen serverError={this.state.serverError} />;
        } else {
            switch (appstate) {
                case 'setup':
                    toRenderd =  (
                        <PageSetup
                            app={this.state}
                            onMessage={this.onMessage}
                            appSetState = {this.appSetState.bind(this)}
                            Logout = {this.Logout.bind(this)}
                            savePages={this.savePages.bind(this)}

                />
                    );
                    break;
                case 'start':
                    toRenderd =  (
                        <PageStart
                            app={this.state}
                            onMessage={this.onMessage}
                            onClickSource={(id) => this.sourceClick(id)}
                            onClickFormat={(id) => this.formatClick(id)}
                            onClickNextBtn={() => this.ClickNextBtn()}
                            savePages={this.savePages.bind(this)}
                            appSetState = {this.appSetState.bind(this)}
                        />
                    );
                    break;

                case 'loadImages':
                    toRenderd =  (
                        <PageLoadImages
                            app={this.state}
                            onMessage={this.onMessage}
                            onClickNextBtn={(this.ClickNextBtn.bind(this))}
                            onClickPrevBtn={() => this.ClickPrevBtn()}
                            savePages={this.savePages.bind(this)}
                            appSetState = {this.appSetState.bind(this)}
                        />
                    );
                    break;

                case 'editor':
                    toRenderd =  (
                        <PageEditor
                            app={this.state}
                            onMessage={this.onMessage}
                            onClickNextBtn={() => this.ClickNextBtn()}
                            onClickPrevBtn={() => this.ClickPrevBtn()}
                            savePages={this.savePages.bind(this)}

                            appSetState = {this.appSetState.bind(this)}
                            appImageInitMask = {this.appImageInitMask.bind(this)}
                            appEditorChanged = {this.appEditorChanged.bind(this)}
                            appImageChanged = {this.appImageChanged.bind(this)}
                            alignInit = {this.alignInit.bind(this)}
                            alignLandscape = {this.alignLandscape.bind(this)}
                            alignPortrait = {this.alignPortrait.bind(this)}



                        />
                    );
                    break;

                case 'print':
                    toRenderd =  (
                        <PagePrint
                            app={this.state}
                            onMessage={this.onMessage}
                            onClickNextBtn={(this.ClickNextBtn.bind(this))}
                            onClickPrevBtn={() => this.ClickPrevBtn()}
                            savePages={this.savePages.bind(this)}
                            appSetState = {this.appSetState.bind(this)}
                            alignInit = {this.alignInit.bind(this)}
                            alignLandscape = {this.alignLandscape.bind(this)}
                            alignPortrait = {this.alignPortrait.bind(this)}
                            appImageInitMask = {this.appImageInitMask.bind(this)}
                        />
                    );
                    break;

                default:
                    toRenderd = (
                        <div className="App">
                            Learn React
                        </div>
                    );
            }

        }


        let appWidth = this.state.appWidth ? this.state.appWidth : '100%';
        let appHeight = this.state.appHeight ? this.state.appHeight : '100%';
        return (
            <div ref={(node)=>{this.appWrapperNode=node}}
                style={{width:appWidth, height:appHeight}}
                className={"app-wrapper"}>
                <CssBaseline />
                <MuiThemeProvider theme={THEME}>
                    <SetupPin app={this.state}
                              appSetState = {this.appSetState.bind(this)}
                              Login = { this.state.isSetup === 1 ? this.Login.bind(this) :
                                        this.state.isSetup === 2 ? this.uploadEnable.bind(this) :
                                        this.Login.bind(this)
                              }
                              Logout = {this.Logout.bind(this)}
                    />
                    {toRenderd}
                    <Snackbar
                        anchorOrigin={{ vertical: 'bottom', horizontal: 'left', }}
                        autoHideDuration={6000}
                        open={(this.state.snackMessage!==null)}
                        onClose={this.onMessageHide}
                        ContentProps={{
                            'aria-describedby': 'message-id',
                        }}
                        message={<span id="message-id">{this.state.snackMessage}</span>}
                        action={[
                            <IconButton
                                key="close"
                                aria-label="Close"
                                color="inherit"
                                className={'classes.close'}
                                onClick={this.onMessageHide}
                            >
                                <CloseIcon />
                            </IconButton>
                        ]}
                    />

                    {this.state.isPleaseWait && (
                        <div className="app-please-wait-wrp position-fixed">
                            <div onClick={()=>{this.setState({isPleaseWait:false})}} className = "app-please-wait spinner-border text-primary" role = "status" >
                                <span className = "sr-only" > Loading...</span>
                            </div>
                        </div>
                    )}

                    <Dialog
                        open={wrongOrientation}
                        onClose={()=>{this.updateDimensions()}}
                        aria-labelledby="alert-dialog-title"
                        aria-describedby="alert-dialog-description"
                    >
                        <DialogTitle id="alert-dialog-title">{t('Wrong orientation')}</DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-description">
                                {t("Please, turn your device to lanscape")}
                            </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                            <button onClick={()=>{this.updateDimensions()}} className={"btn btn-big  btn-primary"} autoFocus>
                                OK
                            </button>
                        </DialogActions>
                    </Dialog>


                </MuiThemeProvider>

            </div>
        )

    }

}

export default App;
