import React from 'react';

import './index.css';
import Icon from "@material-ui/core/Icon";
import Paper from '@material-ui/core/Paper';
import t from "../i18n";
import PageSetupFormats from './PageSetupFormats'
import PageSetupArchive from './PageSetupArchive'
import PageSetupSources from './PageSetupSources'
import PageSetupParameters from './PageSetupParameters'

export default class PageSetup extends React.Component {

    state = {
        currentTab: 'archive',
    };



    componentDidUpdate(prevProps, prevState, snapshot) {
    }

    openTab = (tab) => {
        if(tab!== this.state.currentTab)
            this.setState({currentTab: tab})
    };

    render() {

        let tab = null;
        switch (this.state.currentTab) {
            case 'formats':
                tab = <PageSetupFormats {...this.props}/>;
                break;

            case 'sources':
                tab = <PageSetupSources {...this.props}/>;
                break;

            case 'parameters':
                tab = <PageSetupParameters {...this.props}/>;
                break;


            default:
                tab = <PageSetupArchive {...this.props}/>;
                break;
        }

        return (
            <div className="app-page app-bg-e0 app-setup w-100 h-100">
                <div className="app-setup-wrapper h-100">
                    <div className="app-setup-main h-85 w-100">
                        <div className="app-setup-main-tabcontrol w-15 h-100 d-inline-block p-2">
                            <Paper className="_tab-control w-100 h-100 p-2">
                                <div className="_menuwrapper">
                                    <button className={"btn "+(this.state.currentTab==='formats' ? 'btn-primary' : 'btn-outline-primary')}
                                            onClick={()=>{this.openTab('formats')}}
                                            type="button" > {t('Formats')}
                                    </button>
                                </div>
                                <div className="_menuwrapper">
                                    <button className={"btn "+(this.state.currentTab==='sources' ? 'btn-primary' : 'btn-outline-primary')}
                                            onClick={()=>{this.openTab('sources')}}
                                            type="button" > {t('Sources')}
                                    </button>
                                </div>
                                <div className="_menuwrapper">
                                    <button className={"btn "+(this.state.currentTab==='archive' ? 'btn-primary' : 'btn-outline-primary')}
                                            onClick={()=>{this.openTab('archive')}}
                                            type="button" > {t('Archive')}
                                    </button>
                                </div>
                                <div className="_menuwrapper">
                                    <button className={"btn "+(this.state.currentTab==='parameters' ? 'btn-primary' : 'btn-outline-primary')}
                                            onClick={()=>{this.openTab('parameters')}}
                                            type="button" > {t('Parameters')}
                                    </button>
                                </div>

                            </Paper>
                        </div>
                        <div className="app-setup-main-tab w-85 h-100 d-inline-block p-2">
                            {tab}
                        </div>
                    </div>
                    <div className="app-setup-bottom h-15 p-2">
                            <Paper className="app-setup-bottom-buttons text-center h-100 p-2">
                                <Paper className="float-left h-100 p-2">
                                    {this.props.app.appWidth}x{this.props.app.appHeight}
                                    <div>
                                        <button className={"btn btn-light"}
                                                onClick={()=>{if(window.confirm(t('Refresh application')+'?')) document.location.reload();}}
                                                disabled={false}
                                                title="Refresh"
                                                type="button" >
                                            <Icon>update</Icon> {this.props.app.version}
                                        </button>

                                    </div>
                                </Paper>
                                <div className="m-2 d-inline-block">
                                    <button className={"btn btn-primary btn-big"}
                                            onClick={()=>{this.props.Logout()}}
                                            disabled={false}
                                            title="Logout"
                                            type="button" >
                                        <Icon>close</Icon>
                                    </button>
                                </div>

                            </Paper>

                    </div>
                </div>
            </div>
            )
    }
}

