import React, {PureComponent} from "react";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";

import * as tools from "../../tools";
import Icon from "@material-ui/core/Icon";
import {THEME} from "../../tools";


export default class ImageThumb extends PureComponent {
    constructor(props){   // Optional, declare a class field to improve readability
        super(props);
        this.nodeThumb = null;
        this.scrollRef = null;

        this.state = {
            thumbWidth: '100px',
        }
    }

    componentDidMount() {
        if(this.nodeThumb) {
            if(this.state.thumbWidth !== this.nodeThumb.clientHeight) {
                this.setState({
                    thumbWidth: this.nodeThumb.clientHeight, //this is not an error, need square
                })
            }
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(this.scrollRef && this.props.selected) {
            this.props.scrollIntoView(this.scrollRef);
        }
    }

    render() {
        const image = this.props.image;
        // this.props.image.orientation
        // 1 - no rotate
        // 3 - 180deg
        // 6 - 270deg
        // 8 - 90deg
        let rotate = "";
        switch (image.orientation) {
            case 3:
                rotate = " rotate180";
                break;
            case 6:
                rotate = " rotate90";
                break;
            case 8:
                rotate = " rotate270";
                break;
            default:
                break;
        }
        let boxStyles = {width: this.state.thumbWidth, height: this.state.thumbWidth}; //this is not an error, need square

        let styles = {width: '100%', height: 'auto'};
        if(image.height>image.width) {
            styles = {width: 'auto', height: '100%'};
        }


        let _icon_enabled = 'inherit';
        let _icon_disabled = 'disabled';
        let clrZoom = _icon_disabled;
        let clrFilters = _icon_disabled;
        let clrColors = _icon_disabled;
        if(image.editorState) {
            if(image.editorState.initedImagePosition) clrZoom = _icon_enabled;
            if(image.editorState.brightness!==1) clrFilters = _icon_enabled;
            if(image.editorState.contrast!==1) clrFilters = _icon_enabled;
            if(image.editorState.saturate!==1) clrFilters = _icon_enabled;
            if(image.editorState.sepia) clrFilters = _icon_enabled;
            if(image.editorState.colorR!==1) clrColors = _icon_enabled;
            if(image.editorState.colorG!==1) clrColors = _icon_enabled;
            if(image.editorState.colorB!==1) clrColors = _icon_enabled;
        }


        return (
            <Card className={'app-editor-thumb-item h-100 d-inline-block m-1 position-relative '+(this.props.selected ? ' text-light bg-primary' : ' text-primary bg-light')} >
                <CardContent  className={"p-0 h-100 position-relative"} onClick={this.props.onSelect} >
                    <div ref={(node)=>this.scrollRef=node} className={"app-editor-thumb-item-img-wrapper h-100 p-1"}>
                        <div ref={(node)=>this.nodeThumb=node} style={boxStyles} className={"app-editor-thumb-item-box h-100 border "+rotate} >
                            <span className="app-editor-thumb-item-img-helper"/>
                            <img
                                src={tools.makeGetUri()+encodeURI(image.thumb)}
                                style={styles}
                                alt="" className={"app-editor-thumb-item-image"+(!image.count ? ' _zero' : '')}/>
                        </div>
                    </div>
                    <div className={"app-editor-thumb-item-img-countbubble"}
                        style={{backgroundColor: THEME.palette.primary.main, color:THEME.palette.primary.contrastText }}
                        >
                        <span>{image.count ? image.count : '-'}</span>
                    </div>

                    <div className={"app-editor-thumb-item-img-controls h-100"}>
                        <div className="app-editor-thumb-item-img-controls-flexv pr-1 h-100">

                            <div>
                                <Icon fontSize={'small'} color={clrZoom}>zoom_out_map</Icon>
                            </div>
                            <div>
                                <Icon fontSize={'small'} color={clrFilters}>brightness_medium</Icon>
                            </div>
                            <div>
                                <Icon fontSize={'small'} color={clrColors}>equalizer</Icon>
                            </div>
                            <div className={"app-editor-thumb-item-img-index"}>
                                <span>{this.props.itemIndex+1}</span>
                            </div>
                        </div>


                    </div>
                    {!image.count && (
                        <div className="app-editor-thumb-zeroprint">x</div>
                    )}
                </CardContent>


            </Card>
        )
    }
}
