import React from 'react';
import * as tools from "../../tools";
import axios from "axios";
import t from "../i18n";

import './index.css';
import Icon from "@material-ui/core/Icon";
import Paper from '@material-ui/core/Paper';
import LinearProgress from '@material-ui/core/LinearProgress';
import {makeGetUri} from "../../tools";
import {calculateImageDimensions} from "../../tools";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";

export default class PagePrint extends React.Component {
    state = {
        orderFolder: null,
        orderNum: null,
        savingImages: false,
        orderPrinted: false,
    };

    savePartialProgress(start, processed, party, imageslen) {
        processed /= 2; // increment twice (init + process)
        let progress = Math.ceil((start+processed)/imageslen*100);
        let job_done = start+processed >= imageslen;
        if(job_done) {
            progress = -1;
            if(this.props.app.setup.batchSaveMode==='1') {
                let uri = makeGetUri({'q':'orderFilesProcessBatch', 'folder': this.state.orderFolder});
                axios.get(uri).then(res => {
                    if(res.status===200) {
                        if(res.data.error!=='') {
                            this.props.onMessage(t('orderFilesProcessBatch error:')+res.data.error);
                        }
                    } else {
                        this.props.onMessage(t('orderFilesProcessBatch error status, let see log'));
                        console.log(t('orderFilesProcessBatch error status'), res.status);
                    }
                });
            }

            // job done
        } else {
            if(processed >= party) {
                // partial end
                setTimeout(()=>{ this.saveImage(start+processed); },500);
                // console.log('partial end', start, processed); job_done = true;
            }
        }
        if(this.props.app.progressBarValue !== progress) {
            let isPleaseWait = (progress>-1 && progress<99);
            this.props.appSetState({progressBarValue:progress, isPleaseWait:isPleaseWait, savingImages:!job_done });
        }
        if(job_done)
            this.setState({orderPrinted: true} );
    }

    checkBeforeSaveImage() {
        let total = 0;
        for(let i=0; i<this.props.app.images.length; i++) {
            total += this.props.app.images[i].count;
        }
        if(total) {
            this.saveImage(0);
        } else {
            this.props.onClickNextBtn();
        }
    }
    saveImage = (first) => {
        let party = 10;
        let imageslen = this.props.app.images.length;
        if(first+party>imageslen) {
            party = imageslen - first;
        }

        let processed = 0;
        let orderFolder = '';// '../';
        if(this.state.orderFolder) {
            orderFolder = this.state.orderFolder;
        }
        this.savePartialProgress(first, processed, party, imageslen);
        let uri = makeGetUri({'q':'orderGetFolder', 'folder': orderFolder});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    if(res.data.data) {
                        orderFolder = res.data.data;
                        this.setState({orderFolder: orderFolder, orderNum: res.data.orderNum});

                        let partImages = this.props.app.images.slice(first, first+party);
                        partImages.forEach( (image, index)=>{
                            let i = first+index;
                            processed++; // count init
                            this.savePartialProgress(first, processed, party, imageslen);

                            // console.log('Process image from:', first, 'index:', index, 'key', i );

                            this.autoInitImage(i, ()=>{

                                let image = this.props.app.images[i];
                                if(image.count) {
                                    if(this.props.app.setup.processMode === 'frontend') {
                                        tools.processImage(this.processCanvasNode, this.props.app, i, (DataURL)=>{

                                            let uri = tools.makeGetUri();
                                            const params = new URLSearchParams();
                                            params.append('q', 'orderFileWrite');
                                            params.append('folder', orderFolder);
                                            params.append('image', JSON.stringify(image));
                                            params.append('name', (i+1)+'.'+image.extension);
                                            params.append('count', image.count);
                                            params.append('copy', this.props.app.setup.copiesMode ? this.props.app.setup.copiesMode : 1);
                                            params.append('data', DataURL);

                                            axios.post(uri, params ).then(res => {
                                                if(res.status===200) {
                                                    if(res.data.error!=='') {
                                                        this.props.onMessage('error '+res.data.error);
                                                    }
                                                    processed++;
                                                    // console.log('processed', processed);
                                                    this.savePartialProgress(first, processed, party, imageslen);
                                                }
                                            });
                                        });

                                    } else { // this.props.app.setup.processMode === backend
                                        const p = calculateImageDimensions(this.props.app, i);

                                        let uri = tools.makeGetUri();
                                        const params = new URLSearchParams();
                                        params.append('q', 'orderFileToProcess');
                                        if(this.props.app.setup.batchSaveMode==='1') params.append('batch', '1');
                                        params.append('folder', orderFolder);
                                        params.append('name', (i+1)+'.'+image.extension);
                                        params.append('image', JSON.stringify(image));
                                        params.append('dimensions', JSON.stringify(p));
                                        params.append('count', image.count);
                                        params.append('copy', this.props.app.setup.copiesMode ? this.props.app.setup.copiesMode : 1);

                                        axios.post(uri, params ).then(res => {
                                            if(res.status===200) {
                                                if(res.data.error!=='') {
                                                    this.props.onMessage(t('error')+':'+res.data.error);
                                                }
                                                processed++;
                                                // console.log('processed', processed);
                                                this.savePartialProgress(first, processed, party, imageslen);
                                            }
                                        });
                                    }

                                } else {
                                    processed++;
                                    this.savePartialProgress(first, processed, party, imageslen);
                                }
                            });
                        }, this );

                        // for(let i=first; i<first+party; i++) {
                        //
                        //     // console.log('image init ', first+i);
                        //     processed++; // count init
                        //     this.savePartialProgress(first, processed, party, imageslen);
                        //
                        // }//for

                    }
                } else {
                    this.props.onMessage(t('error')+':'+res.data.error);
                }
            } else {
                this.props.onMessage(t('error status, let see log'));
                console.log('error status', res.status);
            }
        });
    };

    autoInitImage = (i, fn_callback) => {
        let image = this.props.app.images[i];

        this.props.appImageInitMask(i, false, ()=>{

            // console.log(' autoInitImage appImageInitMask', i );

            if(!image.editorState.initedImagePosition) {
                // console.log('init image '+i);
                this.props.alignInit(i, fn_callback);
            } else {
                if( typeof(fn_callback) === 'function') { fn_callback(); }
            }

        });

    };



    render() {

        const formats = this.props.app.formats;
        const images = this.props.app.images;
        let temporary = [];
        let _flen = formats.length;
        for(let i=0; i<_flen; i++) {
            const ims = images.filter((image)=>{
                for (let key in image.format) {
                    if (formats[i][key] === undefined || formats[i][key] !== image.format[key])
                        return false;
                }
                return true;
            });
            temporary.push(ims);
        }
        // console.log(temporary);

        let summary = []; //temporary.filter((t,i)=>t.length>0);
        _flen = temporary.length;
        let totalSum = 0;
        for(let i=0; i<_flen; i++) {
            const tmp = temporary[i];
            if(tmp.length>0) {
                let cnt = 0;
                for(let j=0; j<tmp.length; j++) {
                    cnt += tmp[j].count;
                }
                if(cnt>0) {
                    summary.push({ format: formats[i], count: cnt, });
                    totalSum += formats[i].cost * cnt;
                }
            }
        }
        // console.log(summary);

        return (

            <div className="app-page app-bg-e0 app-page-start w-100 h-100">
                <div className="app-print h-90 p-2">
                    <Paper className="h-100 w-100 p-2 app-bg-e text-center">
                        <Paper className="app-print-costs-paper scrollbar p-2 m-2">
                            <table className="table">
                                <thead>
                                <tr>
                                    <th scope="col" colSpan="2">{t('Format')}</th>
                                    <th scope="col">{t('Amount')}</th>
                                    <th scope="col">{t('Total')}: {totalSum}</th>
                                </tr>
                                </thead>
                                <tbody>
                                {summary.map((sum, i)=>
                                    <tr key={i}>
                                        <td>{sum.format.name} {sum.format.width}" x{sum.format.height}"</td>
                                        <td>{Math.round(sum.format.height*25.4)}x{Math.round(sum.format.width*25.4)} , {t(sum.format.surface)}</td>
                                        <td>{sum.count}</td>
                                        <td>{tools.Round(sum.format.cost * sum.count)}</td>
                                    </tr>
                                ) }
                                </tbody>
                            </table>

                        </Paper>
                        <div className="app-print-costs-buttons text-center">
                            <button className={"btn btn-big btn-primary m-2"}
                                    onClick={() => {this.props.onClickPrevBtn()}}
                                    disabled={false}
                                    title={t('Previous')}
                                    type="button" >
                                <Icon>arrow_back</Icon> {t('Previous')}
                            </button>

                            <button type="button"
                                    className={"btn btn-big btn-success m-2"}
                                    disabled={this.state.savingImages}
                                    onClick={()=>{this.checkBeforeSaveImage()}}>
                                <Icon>print</Icon> {t('Print')}
                            </button>

                        </div>
                    </Paper>


                </div>

                <div className="app-bottomframe w-100 h-10 p-2 position-relative">
                    <Paper className="h-100 w-100 p-2 app-bg-e">
                        {(this.props.app.progressBarValue>=0 && (
                            <div className="app-bottom-progress h-100 p-2 w-100">
                                <LinearProgress variant="determinate" value={this.props.app.progressBarValue} />
                            </div>
                        ))}
                    </Paper>
                </div>

                <Dialog
                    open={this.state.orderPrinted}
                    onClose={this.props.onClickNextBtn}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">{t('Successful printed')}</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                            {t("The number of your order is")}
                            <span className="text-center text-dark" style={{fontSize:'1.4em', display:'block'}}>{this.state.orderNum}</span>
                            {t('Dont forget to remove your source.')}
                            <br/><br/>
                            {t('Thank you!')}
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <button onClick={this.props.onClickNextBtn} className={"btn btn-big  btn-primary"} autoFocus>
                            OK
                        </button>
                    </DialogActions>
                </Dialog>

            </div>
        );
    }
}

