import React from "react";
import * as tools from "../../tools";

export default class ImageEditorColorCanvas extends React.Component {
    constructor(props) {
        super(props);
        this.canvasImg = new Image();
        this.canvasImg.crossOrigin = 'anonymous';
        const _upd = this.updateCanvas.bind(this);
        this.canvasImg.onload = function() {
            _upd();
        };



        this.state = {
            imgSrc: null,
            fakeThins: true,
        }
    }

    componentDidUpdate(prevProps, prevState, snapshot) {

        // console.log('componentDidUpdate');

        if(this.props.app.currentImage!==prevProps.app.currentImage) {
            console.log('new image clicked');
            this.setState({
                imgSrc: null,
            });
            return;
        }

        this.props.appEditorChanged(this.myImgContainer.clientWidth, this.myImgContainer.clientHeight);

        const image = this.props.app.images[this.props.app.currentImage];
        if(!image.editorState.initedMask) {
            this.props.appImageInitMask(this.props.app.currentImage);
            return;
        }

        if(!image.editorState.initedImagePosition) {
            this.props.alignInit(this.props.app.currentImage);
            return;
        }

        let img_src = tools.makeGetUri()+encodeURI(image.src);
        if(this.canvasImg.src!==img_src) {
            const _upd = this.updateCanvas.bind(this);
            this.canvasImg.onload = function() {
                _upd();
            };
            this.canvasImg.src=img_src;
            return;
        }

        // if(image.editorState.needUpdateCanvas) {
        this.updateCanvas();
        // }


    }

    updateCanvas() {

        // console.log('Zoom updateCanvas');
        if(!this.myCanvas || this.myCanvas.clientWidth===0 || this.myCanvas.clientHeight===0) return;

        const ctx = this.myCanvas.getContext('2d');
        let canvasX = this.myCanvas.clientWidth;
        let canvasY = this.myCanvas.clientHeight;
        // console.log('drawImage', this.canvasImg.src, canvasX, canvasY);

        const editorState = this.props.app.images[this.props.app.currentImage].editorState;
        ctx.drawImage(this.canvasImg, 0, 0, canvasX, canvasY);
        let pixelArray = ctx.getImageData(0,0, canvasX, canvasY);

        if(editorState.brightness!==1 || editorState.contrast!==1 || editorState.colorR!==1 || editorState.colorG!==1 || editorState.colorB!==1 ) {
            pixelArray = tools.ColorCorrection(pixelArray, editorState.brightness, editorState.contrast, editorState.colorR, editorState.colorG, editorState.colorB);
        }
        if(editorState.sepia) {
            pixelArray = tools.ColorSepia(pixelArray, 1);
        }
        if(editorState.saturate!==1) {
            pixelArray = tools.saturate(pixelArray, editorState.saturate);
        }
        ctx.putImageData(pixelArray, 0, 0);

        // this.setState({
        //     imgSrc: this.myCanvas.toDataURL('image/jpeg', 0.7),
        // });
        // this.props.appImageChanged( this.props.app.currentImage,{needUpdateCanvas: false})
    }

    render() {

        // console.log('ColorCanvas render');
        const newImage = this.renderedImageIndex !== this.props.app.currentImage;
        this.renderedImageIndex = this.props.app.currentImage;

        const image = this.props.app.images[this.props.app.currentImage];
        if(newImage) {
            let img_src = tools.makeGetUri()+encodeURI(image.src);
            if(this.canvasImg.src!==img_src) {
                this.canvasImg.src=img_src;
            }
        }


        // console.log('render ImageEditorZoomCanvas ');
        let maskTBStyle = {
            height: image.editorState.maskTB,
        };
        let viewportStyles = {
            height: this.props.app.editorHeight - image.editorState.maskTB*2,
            width: this.props.app.editorWidth - image.editorState.maskLR*2,
            top: image.editorState.maskTB,
            left: image.editorState.maskLR,
        };
        let maskLRStyle = {
            width: image.editorState.maskLR,
        };

        let canvasWidth = image.editorState.imgWidth;
        let canvasHeight = image.editorState.imgHeight;
        let canvasStyle = {
            top: image.editorState.sliderPositionTB,
            left: image.editorState.sliderPositionLR,
            transform: 'rotate('+image.editorState.imageRotate+'deg)',
        };

        return (
            <div className="app-main-editor-Printable-Mask border-light fld3d">
                <div className="app-main-editor-Printable-mask _t" style={{height:'1rem'}} />
                <div className="app-main-editor-Printable-mask _l" style={{width:'1rem'}} />

                <div ref={(node)=>{this.myImgContainer=node}} className='app-main-editor-Printable' >

                    <div className="app-main-editor-Printable-mask _t" style={maskTBStyle} />
                    <div className="app-main-editor-Printable-mask _l" style={maskLRStyle} />

                    <div className="app-main-editor-Printable-viewport"
                         style={viewportStyles}>
                        <canvas
                            className="app-main-editor-Printable-img"
                            ref={(node)=>{this.myCanvas=node}}
                            width={ canvasWidth }
                            height={ canvasHeight}
                            style={ canvasStyle }
                        />



                    </div>
                    <div className="app-main-editor-Printable-mask _r" style={maskLRStyle} />
                    <div className="app-main-editor-Printable-mask _b" style={maskTBStyle} />


                </div>

                <div className="app-main-editor-Printable-mask _r" style={{width:'1rem'}} />
                <div className="app-main-editor-Printable-mask _b" style={{height:'1rem'}} />

            </div>


        );
    }
}

