import React from 'react';
import * as tools from "../../tools";
import axios from "axios";

import './index.css';
import Icon from "@material-ui/core/Icon";
import Paper from '@material-ui/core/Paper';
import {makeGetUri} from "../../tools";
import Switch from "@material-ui/core/Switch";
import Fab from "@material-ui/core/Fab";
import t from "../i18n";
import Dialog from "@material-ui/core/Dialog";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";


export default class PageSetupSources extends React.Component {

    state = {
        editedSources: this.props.app.sources.slice(),
        listIcons: [],
        listScripts: [],
        scriptTxt: false,

    };

    componentDidMount() {
        if(!this.state.editedSources) {
            this.setState({
                editedSources: this.props.app.sources.slice()
            });
        }

        let uri = tools.makeGetUri({'q':'setupSourcesListIcons', 'p': this.props.app.isSetupPassword});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    if(res.data.data) {
                        this.setState({listIcons: res.data.data});
                    }
                }
            }
        });

        uri = tools.makeGetUri({'q':'setupSourcesListScripts', 'p': this.props.app.isSetupPassword});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    if(res.data.data) {
                        this.setState({listScripts: res.data.data});
                    }
                }
            }
        });
    }

    viewScript(name) {
        let scriptTxt = false;
        let len = this.state.listScripts.length;
        for(let i=0; i<len; i++) {
            console.log(this.state.listScripts[i].name, name);
            if(this.state.listScripts[i].name === name) {
                console.log('gotcha');
                scriptTxt = this.state.listScripts[i].script;
                break;
            }
        }
        this.setState({scriptTxt: scriptTxt});
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
    }

    sourceNew = ()=>{
        let sources = this.state.editedSources.slice();
        sources.push( Object.assign({}, sources[0]) );
        const last = sources.length-1;
        sources[last].name = 'new source';
        sources[last].enabled = false;

        this.setState({editedSources: sources});

    };

    sourceRemove = (index) => {
        let sources = this.state.editedSources;
        sources.splice(index,1);
        this.setState({editedSources: sources});

    };
    sourceChange = (index, id, ev) => {
        let sources = this.state.editedSources;
        let changed = true;
        switch (id) {
            case 'enabled':
            case 'emptyhide':
            case 'autoclear':
                sources[index][id] = !sources[index][id];
                break;

            case 'name':
            case 'path':
            case 'icon':
            case 'scripton':
            case 'scriptoff':
                sources[index][id] = ev.target.value;
                break;

            default:
                changed = false;

        }
        if(changed)
            this.setState({editedSources: sources});
    };

    sourcesSave() {
        let sources = this.state.editedSources;
        // for(let i=0; i<sources.length; i++) {
        //     console.log(i, sources[i]);
        // }
        let uri = makeGetUri();
        const params = new URLSearchParams();
        params.append('q', 'setupSourcesSave');
        params.append('sources', JSON.stringify(sources));
        params.append('p', this.props.app.isSetupPassword);
        axios.post(uri, params ).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    this.props.appSetState({sources: sources}, ()=>{
                        this.props.onMessage( t('successful saved') );
                    });

                } else {
                    this.props.onMessage('error :'+res.data.error);
                }
            } else {
                console.log('axios.post status ', res.status);
            }
        });
    }

    sourceMove(index,shift) {
        let newIndex = index+shift;

        console.log(newIndex);
        if(newIndex<0 || newIndex>this.state.editedSources.length-1 ) {
            return false;
        }
        let sources = this.state.editedSources.slice();
        let tmp = sources[index];
        sources[index] = sources[newIndex];
        sources[newIndex] = tmp;
        this.setState({editedSources: sources});

    }

    render() {

        let sources = (this.state.editedSources ? this.state.editedSources : []);
        let listIcons = this.state.listIcons;
        let listScripts = this.state.listScripts;

        return (
            <div className="app-page app-bg-e0 app-page-setup-formats w-100 h-100">
                <div className="app-setup  h-100">



                    <div className="app-setup-main h-100">
                        <Paper className="app-setup-sources scrollbar p-2 h-100">
                            <div className="app-setup-sources-header">
                                <div className="_wrp _wrp_enabled"><small>On</small></div>
                                <div className="_wrp _wrp_name"><small>{t('Name')}</small></div>
                                <div className="_wrp _wrp_icondisplay"> </div>
                                <div className="_wrp _wrp_path"><small>{t('Path')}</small></div>
                                <div className="_wrp _wrp_scripton"><small>{t('Script ON / OFF')}</small></div>
                                <div className="_wrp _wrp_emptyhide"><small>{t('Emptyhide')}</small></div>
                                <div className="_wrp _wrp_autoclear"><small>{t('Autoclear')}</small></div>
                                <div className="_wrp _wrp_movecontrol"> </div>
                            </div>

                            {sources.map((source, index)=>
                                <div key={index} className={"app-setup-source-item"+(source.enabled ? '' : ' _disabled' )}>
                                    <div className="_wrp _wrp_enabled">
                                        <Switch
                                            checked={source.enabled}
                                            onChange={(ev) => this.sourceChange(index,'enabled', ev)}
                                            value={'enabled'}
                                            color="primary"
                                        />

                                    </div>
                                    <div className="_wrp _wrp_name">
                                        <div>
                                            <input type="text" value={source.name} onChange={(ev) => this.sourceChange(index,'name', ev)} className="w-100"/>
                                        </div>
                                        <div>
                                            <select value={source.icon} data-value={source.icon} onChange={(ev) => this.sourceChange(index,'icon', ev)}  className="w-100">
                                                {listIcons.map((h, i)=>
                                                    <option key={i} value={h}>{h.replace(/^.*[\\\/]/, '')}</option>
                                                )}
                                            </select>

                                        </div>
                                    </div>
                                    <div className="_wrp _wrp_icondisplay"
                                         style={{backgroundImage: (source.icon ? 'url('+tools.makeGetUri()+source.icon+')' : '')}}
                                    > </div>
                                    <div className="_wrp _wrp_path">
                                        <input type="text" value={source.path} onChange={(ev) => this.sourceChange(index,'path', ev)} className="w-100"/>
                                    </div>
                                    <div className="_wrp _wrp_scripton">
                                        <div>
                                            <select value={source.scripton} onChange={(ev) => this.sourceChange(index,'scripton', ev)}>
                                                <option key={'d'} value=''>{t('---')}</option>
                                                {listScripts.map((h, i)=>
                                                    <option key={i} value={h.name}>{h.name}</option>
                                                )}
                                            </select>
                                            <Icon onClick={()=>{this.viewScript(source.scripton)}} className="text-success" >description</Icon>
                                        </div>
                                        <div>
                                            <select value={source.scriptoff} onChange={(ev) => this.sourceChange(index,'scriptoff', ev)}>
                                                <option key={'d'} value=''>{t('---')}</option>
                                                {listScripts.map((h, i)=>
                                                    <option key={i} value={h.name}>{h.name}</option>
                                                )}
                                            </select>
                                            <Icon onClick={()=>{this.viewScript(source.scriptoff)}} className="text-warning" >description</Icon>

                                        </div>
                                    </div>

                                    <div className="_wrp _wrp_emptyhide">
                                        <Switch
                                            checked={source.emptyhide}
                                            onChange={(ev) => this.sourceChange(index,'emptyhide', ev)}
                                            value={'emptyhide'}
                                            color="primary"
                                        />
                                    </div>
                                    <div className="_wrp _wrp_autoclear">
                                        <Switch
                                            checked={source.autoclear}
                                            onChange={(ev) => this.sourceChange(index,'autoclear', ev)}
                                            value={'autoclear'}
                                            color="primary"
                                        />
                                    </div>
                                    {source.enabled && (
                                        <div className="_wrp _wrp_movecontrol">
                                            <div>
                                                <Icon className="text-primary"
                                                      onClick={()=>{this.sourceMove(index,-1)}}
                                                >arrow_upward</Icon>
                                            </div>
                                            <div>
                                                <Icon className="text-primary"
                                                      onClick={()=>{this.sourceMove(index,1)}}
                                                >arrow_downward</Icon>
                                            </div>
                                        </div>
                                    )}

                                    {!source.enabled && (
                                        <div className="_wrp _wrp_movecontrol">
                                            <Fab className={"app-setup-format-item-remove"}
                                                 onClick={() => this.sourceRemove(index)}
                                                 color="secondary" aria-label="Remove"
                                                 size="small">
                                                <Icon>delete</Icon>
                                            </Fab>
                                        </div>
                                    )}

                                    <hr/>
                                </div>
                            )
                            }
                            <div className="app-setup-format-new">
                                <button className={"btn btn-primary"}
                                        onClick={this.sourceNew}
                                        disabled={false}
                                        title="newFormat"
                                        type="button" >
                                    <Icon>add</Icon>
                                </button>
                                <span className="float-right">
                                    <button className={"btn btn-success "}
                                            onClick={this.sourcesSave.bind(this)}
                                            disabled={false}
                                            title="Save"
                                            type="button" >
                                        <Icon>save</Icon>
                                    </button>

                                </span>
                            </div>

                        </Paper>
                    </div>


                </div>

                <Dialog
                    open={this.state.scriptTxt}
                    onClose={()=>this.setState({scriptTxt: null})}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                    maxWidth={false}
                >
                    <DialogContent>
                        <DialogContentText id="alert-dialog-description" className="app-setup-view-code-wrapper">
                        <textarea className="app-setup-view-code" readOnly>
                            {this.state.scriptTxt}
                        </textarea>
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <button onClick={()=>this.setState({scriptTxt: null})} className={"btn btn-primary"}>
                            {t('Close')}
                        </button>
                    </DialogActions>
                </Dialog>
            </div>
        )
    }
}

