import React from 'react';
import '../index.css';
import Icon from "@material-ui/core/Icon";
import Paper from '@material-ui/core/Paper';


export default class SetupPin extends React.Component {

    passTyped = (ev) => {
        this.props.appSetState({isSetupPassword: ev.target.value});
    };

    render() {
        if (!this.props.app.isSetup || this.props.app.isSetupLogged) return null;

        return (
            <div className="app-page-login p-2 w-100 h-100">
                <div className="app-login-wrapper-bkg app-bg-e0 app-page-start p-2 w-100 h-100"> </div>

                <div className="app-main app-login-wrapper w-100">
                    <Paper className="app-login p-2">
                        <form onSubmit={(ev)=>this.props.Login(ev)}>
                            <div className="form-group">
                                <label htmlFor="p">Pin</label>
                                <input id="p" type="text" autoComplete={'off'} className={"app-login-pass form-control text-center"} value={this.props.isSetupPassword} onChange={this.passTyped} autoFocus/>
                            </div>
                            <div className="m-2 d-inline-block">
                                <button className={"btn btn-big btn-success"}
                                        disabled={false}
                                        title="Login"
                                        type="submit" >
                                    <Icon>vpn_key</Icon>
                                </button>
                            </div>

                            <div className="m-2 d-inline-block">
                                <button className={"btn btn-big btn-primary"}
                                        onClick={this.props.Logout}
                                        disabled={false}
                                        title="Logout"
                                        type="button" >
                                    <Icon>close</Icon>
                                </button>
                            </div>
                        </form>
                    </Paper>

                </div>

            </div>

        )
    }
}

