import React from 'react';
import axios from "axios";
import * as tools from "../../tools";


import './index.css';
import Icon from "@material-ui/core/Icon";
import Paper from '@material-ui/core/Paper';
import t from "../i18n";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogActions from "@material-ui/core/DialogActions";


export default class PageSetupArchive extends React.Component {

    state = {
        arhives: [],
        currentArchive: null,
        isLoading: false,
        archiveFiles: [],
        currentArchiveFile: null,
        isMultipleSelectOrders: false,
        isDeleteConfirmation: 0,
    };


    componentDidMount() {
        this.loadList();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
    }

    loadList() {
        if(this.state.isLoading) return;

        let uri = tools.makeGetUri({'q':'loadArchives', 'p': this.props.app.isSetupPassword});
        this.setState({arhives:[], currentArchive:null, archiveFiles:[], isLoading:true});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    if(res.data.data) {
                        let arhives = res.data.data.map((d)=>{return {folder:d, selected:false}});
                        this.setState({arhives: arhives});
                    }
                }
            }
            this.setState({isLoading: false});
        });

    }

    tryDeleteArchive() {
        let archCount = 0;
        const isMultipleSelectOrders = this.state.isMultipleSelectOrders;
        if (isMultipleSelectOrders) {
            let as = this.state.arhives.filter((a)=>{return a.selected});
            console.log(as);
            archCount = as.length;
        }
        if(archCount>0)
            this.setState({isDeleteConfirmation: archCount});
    }
    deleteArchive() {
        const isMultipleSelectOrders = this.state.isMultipleSelectOrders;
        if (isMultipleSelectOrders) {
            let len = this.state.arhives.length;
            for( let i=0; i<len; i++) {
                if(this.state.arhives[i].selected)
                    this._remove(this.state.arhives[i].folder);
            }
        }

        this.setState({isDeleteConfirmation:0, isMultipleSelectOrders:false, currentArchive:null, archiveFiles:[]});
    }
    _remove(folder) {
        let uri = tools.makeGetUri({'q':'removeArchive', folder: folder, 'p': this.props.app.isSetupPassword});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    this.loadList();
                }
            }
        });
    }

    toggleMultiple() {
        const isMultipleSelectOrders = !this.state.isMultipleSelectOrders;
        if (!isMultipleSelectOrders) {
            let arhives = this.state.arhives.slice();
            let len = arhives.length;
            for( let i=0; i<len; i++) {
                arhives[i].selected = false;
            }
            this.setState({arhives: arhives, isMultipleSelectOrders: isMultipleSelectOrders});
        } else {
            this.setState({isMultipleSelectOrders: isMultipleSelectOrders});
        }
    }

    selectArchive(index) {
        if(this.state.isMultipleSelectOrders) {
            let arhives = this.state.arhives.slice();
            arhives[index].selected = !arhives[index].selected;
            this.setState({arhives: arhives, currentArchive: index});
        } else {
            this.setState({currentArchive: index});
        }
        if(index===this.state.currentArchive) return;

        let uri = tools.makeGetUri({'q':'loadArchive', folder: this.state.arhives[index].folder, 'p': this.props.app.isSetupPassword});
        this.setState({archiveFiles: [], isLoading: true});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    if(res.data.data) {
                        this.setState({archiveFiles: res.data.data});
                    }
                }
            }
            this.setState({isLoading: false, currentArchiveFile: null});
        });
    }

    selectFile(index) {
        this.setState({currentArchiveFile: index});
    }

    render() {
        let archiveCount = 0;
        let archiveTotal = 0;
        let len = this.state.archiveFiles.length;
        for(let i=0; i<len; i++) {
            archiveCount += this.state.archiveFiles[i].count;
            archiveTotal += this.state.archiveFiles[i].format.cost * this.state.archiveFiles[i].count;
        }

        let archiveFile = null;
        if(this.state.currentArchiveFile>=0 && this.state.archiveFiles[this.state.currentArchiveFile]) {
            archiveFile = this.state.archiveFiles[this.state.currentArchiveFile];
            // console.log(archiveFile);
        }

        const isMultipleSelectOrders = this.state.isMultipleSelectOrders;
        let selectedArchiveCount = 0;
        if (isMultipleSelectOrders) {
            let as = this.state.arhives.filter((a)=>{return a.selected});
            console.log(as);
            selectedArchiveCount = as.length;
        }

        return (
            <div className="app-page app-bg-e0 app-page-setup-formats w-100 h-100">
                <div className="app-setup  h-100">



                    <div className="app-setup-main h-100">
                        <Paper className="app-setup-arhives p-2 h-100 w-100 d-flex flex-column">
                            <div className="_listswrp flex-grow-1 h-75">
                                <div className="h-100 d-flex flex-row">
                                    <div className="app-setup-archives-list scrollbar flex-grow-0 border position-relative">
                                        {(this.state.isLoading) && (
                                            <div className = "spinner-border text-primary position-absolute" role = "status" >
                                                <span className = "sr-only" > Loading...</span>
                                            </div>
                                        )}

                                        {this.state.arhives.map((a, i)=>
                                        <div key={i} className={"_item"+(i===this.state.currentArchive ? ' bg-primary text-light' : ' bg-light')}
                                             onClick={()=>{this.selectArchive(i)}}
                                        >

                                            {isMultipleSelectOrders && (
                                                <Icon fontSize={"inherit"} className="text-success">{a.selected ? 'check_circle' : 'radio_button_unchecked'}</Icon>
                                            )}
                                            {a.folder}

                                        </div>
                                        ) }
                                    </div>
                                    <div className="app-setup-archive-files scrollbar flex-grow-0 border position-relative">
                                        {(this.state.isLoading) && (
                                            <div className = "spinner-border text-primary position-absolute" role = "status" >
                                                <span className = "sr-only" > Loading...</span>
                                            </div>
                                        )}
                                        {this.state.archiveFiles.map((f, i)=>
                                            <div key={i} className={"_item"+(i===this.state.currentArchiveFile ? ' bg-primary text-light' : ' bg-light')}
                                                 onClick={()=>{this.selectFile(i)}}
                                            >
                                                {f.name}

                                            </div>
                                        ) }
                                    </div>
                                    <div className="app-setup-archive-fileview flex-grow-1 border h-100">
                                        <div className="d-flex flex-column h-100">
                                            <div className="_view-img border bg-light flex-grow-1"
                                                 style={{backgroundImage: (archiveFile ? "url('"+tools.makeGetUri()+archiveFile.file+"')" : '')}}
                                            > </div>
                                            <div className="_infobottom flex-grow-0">
                                                <table className="table">
                                                    <thead>
                                                    <tr>
                                                        <th scope="col" colSpan="2">{t('Format')}</th>
                                                        <th scope="col">{t('Amount')}</th>
                                                        <th scope="col">{t('Date')}</th>
                                                        <th scope="col">{t('Prints')}</th>
                                                        <th scope="col">{t('Total')}</th>
                                                    </tr>
                                                    </thead>
                                                    <tbody>
                                                        <tr>
                                                            <td>{archiveFile ? archiveFile.format.name+' '+archiveFile.format.width+'"x'+archiveFile.format.height+'"' : ''}</td>
                                                            <td>{archiveFile ? Math.round(archiveFile.format.height*25.4)+'x'+Math.round(archiveFile.format.width*25.4)+' , '+t(archiveFile.format.surface) : '' }</td>
                                                            <td>{archiveFile ? archiveFile.count : ''}</td>
                                                            <td>{archiveFile ? archiveFile.date+' '+archiveFile.time : ''}</td>
                                                            <td>{archiveCount}</td>
                                                            <td>{tools.Round(archiveTotal)}</td>
                                                        </tr>
                                                    </tbody>
                                                </table>


                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>
                            <div className="_btnswrp flex-grow-0">
                                <div className="m-2 d-inline-block">
                                    <button className={"btn btn-success btn "}
                                            onClick={()=>{this.loadList()}}
                                            title="sample"
                                            type="button" >
                                        <Icon>refresh</Icon>
                                    </button>

                                </div>
                                <div className="m-2 d-inline-block">
                                    <button className={"btn"+(isMultipleSelectOrders ? ' btn-outline-success':' btn-outline-secondary')}
                                            onClick={()=>{this.toggleMultiple()}}
                                            title="multiple"
                                            type="button" >
                                        <Icon>filter_none</Icon>
                                    </button>
                                </div>
                                <div className="m-2 d-inline-block">
                                    <button className={"btn btn-success btn "}
                                            onClick={()=>{this.tryDeleteArchive()}}
                                            disabled={selectedArchiveCount===0}
                                            title="Delete"
                                            type="button" >
                                        <Icon>delete_forever</Icon>
                                    </button>

                                </div>


                            </div>
                        </Paper>
                    </div>


                </div>


                <Dialog
                    open={this.state.isDeleteConfirmation>0}
                    onClose={()=>this.setState({isDeleteConfirmation:0})}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">{t("Warning")}</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                            {t('You are selected archives to remove:')} <span className="text-center text-dark" style={{fontSize:'1.4em'}}>{this.state.isDeleteConfirmation>0 ? this.state.isDeleteConfirmation : ''}</span>
                            <br/>
                            {t('Are you sure?')}
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <button onClick={()=>this.deleteArchive()} className={"btn btn-big btn-light"}>
                            {t('OK')}
                        </button>
                        <button onClick={()=>this.setState({isDeleteConfirmation:0})} className={"btn btn-big btn-primary"} autoFocus>
                            {t('Cancel')}
                        </button>
                    </DialogActions>
                </Dialog>

            </div>
        )
    }
}

