import React from "react";
import {THEME} from "../../tools";
import Slider from "@material-ui/lab/Slider";
import ImageEditorColorCanvas from "./ImageEditorColorCanvas";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";

export default class ImageEditorBrightness extends React.Component {

    sliderChangeColorR =  (event, value)=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, colorR: value} );

        // this.props.setStateFromChild({ colorR: value, needUpdateCanvas: true }, ()=>{
        //         this.props.setStateFromChild({needUpdateCanvas: false});
        //     })
    };
    sliderChangeColorG =  (event, value)=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, colorG: value} );
        // this.props.setStateFromChild({ colorG: value, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderChangeColorB =  (event, value)=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, colorB: value} );
        // this.props.setStateFromChild({ colorB: value, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };

    sliderResetColorR =  ()=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, colorR: 1} );
        // this.props.setStateFromChild({ colorR: 1, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderResetColorG =  ()=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, colorG: 1} );
        // this.props.setStateFromChild({ colorG: 1, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderResetColorB =  ()=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, colorB: 1} );
        // this.props.setStateFromChild({ colorB: 1, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderChangeSepia = () => {
        this.props.appImageChanged(this.props.app.currentImage, {
            sepia: !this.props.app.images[this.props.app.currentImage].editorState.sepia,
            colorR: 1,
            colorG: 1,
            colorB: 1,
            needUpdateCanvas: true,
        });
        // this.props.setStateFromChild({
        //     sepia: !this.props.parentState.sepia,
        //     colorR: 1,
        //     colorG: 1,
        //     colorB: 1,
        //     needUpdateCanvas: true,
        // }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };



    render() {

        const image = this.props.app.images[this.props.app.currentImage];

        return (
            <div className="h-100 d-flex flex-row">
                <div className="app-main-editor-visual-left h-90  flex-grow-0 flex-shrink-0">
                </div>
                <div className="app-main-editor-visual-container h-100  flex-grow-1 flex-shrink-1">
                    <div className="h-90">
                        <ImageEditorColorCanvas
                            {...this.props}
                        />
                    </div>
                    <div className="app-main-editor-visual-bottom h-10 w-100">

                    </div>
                </div>
                <div className="app-main-editor-visual-right h-90  flex-grow-0 flex-shrink-0">
                    <div className="h-75">

                        <div className="vert-slider-wrapper h-100 position-relative d-inline-flex">
                            <div className="_reset app-slider-icon"
                                 style={{backgroundColor: THEME.palette.primary.main, color:THEME.palette.primary.contrastText }}
                                 onClick={this.sliderResetColorR}>R</div>
                            <Slider className='app-slider-for-filter'
                                    classes={{track: '_trackR', thumb: '_thumbR'}}
                                    step={0.05}
                                    max={2}
                                    value={image.editorState.colorR}
                                    min={0}
                                    onChange={this.sliderChangeColorR}
                                    disabled={image.editorState.sepia}
                                    vertical
                            />
                            {this.props.app.isDebugShow && (
                            <div className={'_value'}><small>({image.editorState.colorR})</small></div>
                            )}
                        </div>

                        <div className="vert-slider-wrapper h-100 position-relative d-inline-flex">
                            <div className="_reset app-slider-icon"
                                 style={{backgroundColor: THEME.palette.primary.main, color:THEME.palette.primary.contrastText }}
                                 onClick={this.sliderResetColorG}>G</div>
                            <Slider className='app-slider-for-filter'
                                    classes={{track: '_trackG', thumb: '_thumbG'}}
                                    step={0.05}
                                    max={2}
                                    value={image.editorState.colorG}
                                    min={0}
                                    onChange={this.sliderChangeColorG}
                                    disabled={image.editorState.sepia}
                                    vertical
                            />
                            {this.props.app.isDebugShow && (
                            <div className={'_value'}><small>({image.editorState.colorG})</small></div>
                            )}
                        </div>

                        <div className="vert-slider-wrapper h-100 position-relative d-inline-flex">
                            <div className="_reset app-slider-icon"
                                 style={{backgroundColor: THEME.palette.primary.main, color:THEME.palette.primary.contrastText }}
                                 onClick={this.sliderResetColorB}>B</div>
                            <Slider className='app-slider-for-filter'
                                    classes={{track: '_trackB', thumb: '_thumbB'}}
                                    step={0.05}
                                    max={2}
                                    value={image.editorState.colorB}
                                    min={0}
                                    onChange={this.sliderChangeColorB}
                                    disabled={image.editorState.sepia}
                                    vertical
                            />
                            {this.props.app.isDebugShow && (
                            <div className={'_value'}><small>({image.editorState.colorB})</small></div>
                            )}
                        </div>
                    </div>
                    <div className="h-25">
                        <div className="hori-slider-wrapper w-100 position-relative d-inline-flex">
                            <FormControlLabel
                                control={
                                    <Switch
                                        checked={image.editorState.sepia}
                                        onChange={this.sliderChangeSepia}
                                        value="true"
                                        color="primary"
                                    />
                                }
                                label="Sepia"
                            />
                        </div>

                    </div>


                </div>
            </div>

        )

    }
}

