import React from 'react';
import * as tools from "../../tools";
import t from "../i18n";
import {getLangs} from "../i18n";
import {setLang} from "../i18n";
import './index.css';
import Icon from '@material-ui/core/Icon';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import Paper from "@material-ui/core/Paper";
import axios from "axios";
import LinearProgress from "@material-ui/core/LinearProgress";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";

export default class PageStart extends React.Component {
    constructor(props) {
        super(props);
        this.checkSourceTimer = null;
        this.checkSourceTimerMax = 7200; // about 10 hours
        this.checkSourceTimerCount = this.checkSourceTimerMax;

        this.state = {
            isLangsOpen: false,
        }
    }


    componentWillUnmount() {
        if(this.checkSourceTimer) clearTimeout(this.checkSourceTimer);
    }

    componentDidMount() {
        let sources = this.props.app.sources.slice();
        let len = sources.length;

        for(let i=0; i<len; i++)
            sources[i].hasFiles = false;
        this.props.appSetState({sources : sources}, ()=>{
            this.checkSources(true);
        });
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if(prevProps.app.isUploadEnable !== this.props.app.isUploadEnable && this.props.app.isUploadEnable){
            if(this.inputUpload) this.inputUpload.click();
        }
    }

    checkSources(initRefresh) {
        if(!this.props.app.sources) return;

        let sources = this.props.app.sources.slice();
        let len = sources.length;

        if(initRefresh) {
            this.checkSourceTimerCount = 0;
        }

        for(let i=0; i<len; i++) {
            if(sources[i].enabled && sources[i].emptyhide) {
                let uri = tools.makeGetUri({'q':'checkEmptySource', 'id': i});
                axios.get(uri).then(res => {
                    if(res.status===200) {
                        if(res.data.error==='') {
                            sources[i].hasFiles = res.data.data;
                            this.props.appSetState({sources : sources});
                        }
                    }

                });
            }
        }
        if(this.checkSourceTimerCount<this.checkSourceTimerMax)
            this.checkSourceTimer = setTimeout( this.checkSources.bind(this), 9000 );

        this.checkSourceTimerCount++;
    }


    clientUploadProgress = (progress) => {
        let percentage = Math.floor((progress.loaded * 100) / progress.total);
        // console.log(myFileId)
        this.props.appSetState({ progressBarValue: percentage });

        // console.log(percentage);
    };

    clientUpload = (event)=>{
        let formData = new FormData();
        let uploadfiles = event.target.files;

        if(uploadfiles.length===0) return;

        this.props.appSetState({isPleaseWait:true});
        let config = {
            onUploadProgress: (Ev)=>this.clientUploadProgress(Ev),
            headers: { 'Content-Type': 'multipart/form-data' },
        };

        // formData.append("q", 'src-upload');
        for (let i=0; i<uploadfiles.length; i++) {
            formData.append("images[]", uploadfiles[i]);
        }

        axios.post( tools.makeGetUri({'q':'src-upload', 'p':this.props.app.isSetupPassword}), formData, config )
            .then(()=>{
                this.checkSources();
            })
            .catch((error)=>{
                console.log(error);
            })
            .finally(()=>{
                if(event.target) event.target.files = null;
                this.props.appSetState({isPleaseWait:false, progressBarValue:-1});
            });
    };

    openLangsPanel=(key)=>{
        if(key) {
            setLang(key);
        }
        this.setState({isLangsOpen:!this.state.isLangsOpen});
    };



    render() {
        let sources = (this.props.app.sources ? this.props.app.sources : []);
        let langs = getLangs();
        let langsList = this.state.isLangsOpen ? (
            <ClickAwayListener onClickAway={()=>{this.openLangsPanel()}}>
                <Paper className="app-main-change-langs scrollbar position-absolute">
                    <table className="table-langs">
                        {langs.map( (lang, index)=>
                            <tbody key={index} onClick={()=>this.openLangsPanel(lang.key)}>
                            <tr>
                                <th scope="row" className="text-left"><Icon>language</Icon> {lang.caption}</th>
                            </tr>
                            </tbody>

                            )}
                    </table>
                </Paper>
            </ClickAwayListener>
        ) : null;


        return (
            <div className="app-page app-bg-e0 app-page-start w-100 h-100">
                <div className="app-main h-85">
                    <div className="app-main-selectors w-85 h-100 d-inline-block">
                        <div  className="app-main-selectors-source text-center w-50 h-100 p-2 d-inline-block">
                            <Paper className="scrollbar p-2 h-100 app-bg-e  position-relative">
                                {sources.map( (source, index)=>
                                    <Card key={index}
                                          className={'app-source-item d-inline-block m-2'+(this.props.app.currentSource===index ? ' text-white bg-primary' : '')+(source.enabled ? '' : ' app-source-disabled')+((source.emptyhide && !source.hasFiles) ? ' app-source-emptyhide' : '')}
                                          onClick={() => this.props.onClickSource(index)}
                                    >
                                        <CardContent>
                                            <div className="card-img">
                                                <img src={tools.makeGetUri()+source.icon} alt={source.name} className="app-source-icon"/>
                                            </div>
                                            {source.enabled && source.name}

                                        </CardContent>
                                    </Card>
                                )}

                            </Paper>
                        </div>
                        <div className="app-main-selectors-format w-50 h-100 p-2 d-inline-block position-relative">
                            <Paper className="scrollbar p-2 h-100 app-bg-e  position-relative">
                                {this.props.app.formats.map( (format, index)=>format.enabled ?
                                    <Card key={index}
                                          className={'app-format-item m-2'+(this.props.app.currentFormat===index ? ' text-white bg-primary' : '')}
                                          onClick={() => this.props.onClickFormat(index)}
                                    >
                                        <CardContent>
                                            <div className="app-main-selectors-format-wrp">
                                                <div className="_w30">{format.name}</div>
                                                <div className="_w20">
                                                    {format.height}"x {format.width}"
                                                </div>
                                                <div className="_w20">
                                                    {Math.round(format.height*25.4)}x{Math.round(format.width*25.4)}
                                                </div>
                                                <div className="_w15">{t(format.surface)}</div>
                                                <div className="_w15 text-right">{tools.Round(format.cost)}</div>
                                            </div>

                                        </CardContent>
                                    </Card>
                                :
                                    ''
                                )}
                            </Paper>
                        </div>

                    </div>

                    <div className="app-main-rightwidget w-15 h-100 p-2 d-inline-block">
                        <Paper className="h-100 p-2 app-bg-e position-relative">
                            <div className="_fabwrapper">
                                <button
                                    className={"btn btn-big btn-primary"}
                                    disabled={this.props.app.currentSource===null || this.props.app.currentFormat===null}
                                    onClick={() => this.props.onClickNextBtn()}
                                    type="button">
                                    <Icon>arrow_forward</Icon> {t('Next')}
                                </button>
                            </div>

                            <div className="_fabwrapper">
                                <button
                                    className="btn btn-big btn-success mt-4"
                                    disabled={this.checkSourceTimerCount<this.checkSourceTimerMax}
                                    onClick={() => this.checkSources(true)}
                                    type="button">
                                    <Icon>refresh</Icon> {t('Refresh')}
                                </button>
                            </div>


                            <div className="bottom-absolute w-100">

                                <div className="_fabwrapper">
                                    <div className="app-main-change-langs-wrapper position-relative">
                                        {langsList}
                                        <button className="btn btn-big btn-light text-success"
                                                style={{minWidth:'10vh', width:'auto'}}
                                                onClick={this.openLangsPanel}
                                                disabled={false}
                                                title={t('Language')}
                                                type="button" >
                                            <Icon>language</Icon>
                                            <div>
                                                {t('langCaption')}
                                            </div>
                                        </button>

                                    </div>
                                </div>


                                <div className="_fabwrapper">
                                    <button type="button"
                                            className={"btn btn-big btn-success"}
                                            disabled={false}
                                            onClick={()=>{this.props.appSetState({isSetup:1})}}>
                                        <Icon>build</Icon>
                                        <div>
                                            {this.props.app.version}
                                        </div>
                                    </button>
                                </div>

                            </div>


                        </Paper>
                    </div>

                </div>

                <div className="app-bottomframe p-2 h-15 w-100">
                    <Paper className="p-2 app-bg-e h-100 w-100 app-bottom-msg-wrp position-relative">

                        {this.props.app.isUploadEnable && (
                            <label htmlFor="file-input">
                                <input id="file-input" ref={input=>this.inputUpload=input} type="file" multiple onChange={(e)=>this.clientUpload(e)} />
                                <Icon className="_icon text-white text-success">info</Icon>
                            </label>

                        )}
                        {!this.props.app.isUploadEnable && (
                            <Icon onClick={()=>{this.props.appSetState({isSetup:2})}} className="_icon text-white">info</Icon>
                        )}

                        <div className="_msg">
                            {t('Select CD, flash card or other source, then select format what you want, and press button')} '{t('Next')}'
                        </div>

                        {(this.props.app.progressBarValue>=0 && (
                            <div className="app-bottom-progress h-100 p-2 w-100">
                                <LinearProgress variant="determinate" value={this.props.app.progressBarValue} />
                            </div>
                        ))}

                    </Paper>
                </div>

            </div>


        );
    }
}

