import React from 'react';
import * as tools from "../../tools";
import axios from "axios";

import './index.css';
import Paper from '@material-ui/core/Paper';
import {makeGetUri} from "../../tools";
import t from "../i18n";
import Switch from "@material-ui/core/Switch";


export default class PageSetupParameters extends React.Component {

    state = {
        inputNP: '',
        inputNP2: '',
        setup: this.props.app.setup,
        removeOlder: !(this.props.app.setup.removeArchiveOlder<14 || this.props.app.setup.removeArchiveOlder>5000),
    };

    savePass() {
        if(this.state.inputNP!==this.state.inputNP2) {
            this.props.onMessage(t('New password and his confirm does not match'));
            this.setState({inputNP:'', inputNP2:''});
            return;
        }
        let uri = tools.makeGetUri({'q':'setupPasswordSave', 'value': this.state.inputNP, 'p':this.props.app.isSetupPassword});
        axios.get(uri).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    this.props.onMessage(t('New password saved'));
                    this.setState({inputNP:'', inputNP2:''})
                } else {
                    this.props.onMessage(t('error')+res.data.error);
                    console.log(res.data.error);
                }
            }
        });
    }

    saveParameters() {
        let uri = makeGetUri();
        const params = new URLSearchParams();
        params.append('q', 'setupParametersSave');
        let setup = Object.assign(this.state.setup);
        if(setup.removeArchiveOlder<14 || setup.removeArchiveOlder>5000) {
            setup.removeArchiveOlder = 0;
            this.setState( {setup: setup, removeOlder: false} );
        }
        params.append('setup', JSON.stringify(setup));
        params.append('p', this.props.app.isSetupPassword);
        axios.post(uri, params ).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    this.props.onMessage(t('Parameters saved'));
                } else {
                    this.props.onMessage(t('error')+res.data.error);
                    console.log(res.data.error);
                }
            } else {
                console.log('axios.post status ', res.status);
            }
        });

    }




    inputTyped = (ev , key) => {
        let state = {};
        state[key] = ev.target.value;
        this.setState(state);
    };

    removeOlderToggle() {
        this.setState( {removeOlder: !this.state.removeOlder} );
    }

    setupChange(key, ev) {
        let setup = Object.assign(this.state.setup);
        let changed = true;
        switch (key) {

            case 'processMode':
            case 'batchSaveMode':
            case 'copiesMode':
            case 'printNaming':
            case 'copiesNaming':
            case 'removeArchiveOlder':

                setup[key] = ev.target.value;
                break;


            default:
                changed = false;

        }
        if(changed)
            this.setState( {setup: setup} );

    }

    render() {

        let namingOption = [
            '_4x6_102x152_glance',
            '_102x152_glance',
            '_4x6_glance',
            '_102x152',
            '_4x6',
            '_4x6_102x152',
        ];

        return (
            <div className="app-page app-bg-e0 app-page-setup-formats w-100 h-100">
                <div className="app-setup  h-100">



                    <div className="app-setup-main h-100">
                        <Paper className="app-setup-formats scrollbar p-4 mr-2">
                            <div className="m-2">
                                <form>
                                    <div className="form-group">
                                        <label htmlFor="np">{t('New password')}</label>
                                        <input id="np" type="text" autoComplete={'off'} className={"app-login-pass form-control text-center"} value={this.state.inputNP} onChange={(e)=>this.inputTyped(e,'inputNP')} autoFocus/>
                                    </div>
                                    <div className="form-group">
                                        <label htmlFor="np2">{t('Retype password')}</label>
                                        <input id="np2" type="text" autoComplete={'off'} className={"app-login-pass form-control text-center"} value={this.state.inputNP2} onChange={(e)=>this.inputTyped(e,'inputNP2')} />
                                    </div>

                                    <div className="p-2 text-right">
                                        <button className={"btn btn-primary"}
                                                onClick={this.savePass.bind(this)}
                                                disabled={false}
                                                title="save"
                                                type="button" >
                                            {t('Save')}
                                        </button>

                                    </div>
                                </form>

                            </div>

                        </Paper>
                        <Paper className="app-setup-formats scrollbar p-4 mr-2">
                            <div className="m-2">
                                <form>

                                    <div className="form-group">
                                        <label htmlFor="processmode">{t('Process mode')}</label>
                                        <select  id="processmode" value={this.state.setup.processMode} onChange={(ev) => this.setupChange('processMode', ev)} className={"form-control"}>
                                            <option key={'frontend'} value='frontend'>DOESN'T WORK{t('frontend')}</option>
                                            <option key={'backend'} value='backend'>{t('backend')} ({t('Recomended')})</option>
                                        </select>
                                    </div>

                                    <div className="form-group">
                                        <label htmlFor="batch">{t('Print mode')}</label>
                                        <select  id="batch" value={this.state.setup.batchSaveMode} onChange={(ev) => this.setupChange('batchSaveMode', ev)} className={"form-control"}>
                                            <option value='0'>DOESN'T WORK {t('Wait for each file')}</option>
                                            <option value='1'>{t('Fast batch on backend')} ({t('Recomended')})</option>
                                        </select>
                                    </div>

                                    <div className="form-group">
                                        <label htmlFor="copiesMode">{t('Print copies')}</label>
                                        <select  id="copiesMode" value={this.state.setup.copiesMode} onChange={(ev) => this.setupChange('copiesMode', ev)} className={"form-control"}>
                                            <option value='1'>{t('Make copy for each file')}</option>
                                            <option value='0'>{t('Add count to name of file')} ({t('Recomended')})</option>
                                        </select>
                                    </div>

                                    <div className="form-group">
                                        <label htmlFor="printNaming">{t('Print naming')}</label>
                                        <select  id="printNaming" value={this.state.setup.printNaming} onChange={(ev) => this.setupChange('printNaming', ev)} className={"form-control"}>
                                            {namingOption.map((t,n)=>
                                                <option value={n}>{t}</option>
                                            )}
                                        </select>
                                    </div>

                                    <div className="form-group">
                                        <label htmlFor="copiesNaming">{t('Copies naming')}</label>
                                        <select  id="copiesNaming" value={this.state.setup.copiesNaming} onChange={(ev) => this.setupChange('copiesNaming', ev)} className={"form-control"}>
                                            <option value='0'>999_copy2{namingOption[this.state.setup.printNaming]}</option>
                                            <option value='1'>999{namingOption[this.state.setup.printNaming]}_copy2</option>
                                            <option value='2'>999{namingOption[this.state.setup.printNaming]}_c2</option>
                                            <option value='3'>999_c2{namingOption[this.state.setup.printNaming]}</option>
                                        </select>
                                    </div>


                                    <div className="form-group">
                                        <label htmlFor="removeArchiveOlder">{t('Remove archives, older than days')}</label>
                                        <Switch
                                            checked={this.state.removeOlder}
                                            onChange={(ev) => this.removeOlderToggle()}
                                            value={'enabled'}
                                            color="primary"
                                        />
                                        <input type="text" value={this.state.setup.removeArchiveOlder} onChange={(ev) => this.setupChange('removeArchiveOlder', ev)}/>

                                    </div>


                                    <div className="p-2 text-right">
                                        <button className={"btn btn-primary"}
                                                onClick={this.saveParameters.bind(this)}
                                                disabled={false}
                                                title="save"
                                                type="button" >
                                            {t('Save')}
                                        </button>

                                    </div>
                                </form>

                            </div>
                        </Paper>
                    </div>


                </div>
            </div>
        )
    }
}

