import React from "react";
import Slider from "@material-ui/lab/Slider";
import Icon from "@material-ui/core/Icon";
import ImageEditorZoomCanvas from "./ImageEditorZoomCanvas"
import * as tools from "../../tools";
import Fab from "@material-ui/core/Fab";

export default class ImageEditorZoom extends React.Component {
    // constructor(props) {
    //     super(props);
    //     this.updateCanvasTimer = null;
    //
    //     this.state = {
    //         needUpdateCanvas: false,
    //     }
    // }


    sliderZoom = (event, value)=>{
        const state = this.props.app;
        const image = state.images[state.currentImage];

        let imageZoom = value / 100;

        let imgWidth = tools.dpi_inch2dots(image.width, image.format.dpi)*imageZoom*image.editorState.ratio;
        let imgHeight = tools.dpi_inch2dots(image.height, image.format.dpi)*imageZoom*image.editorState.ratio;

        let L = (image.editorState.imgWidth - imgWidth)/2;
        let T = (image.editorState.imgHeight - imgHeight)/2;

        // state.needUpdateCanvas = true;
        // state.images[state.currentImage].editorState.imageZoom = imageZoom;
        // state.images[state.currentImage].editorState.sliderPositionTB = image.sliderPositionTB + T;
        // state.images[state.currentImage].editorState.sliderPositionLR = image.sliderPositionLR + L;
        // state.images[state.currentImage].editorState.imgWidth = imgWidth;
        // state.images[state.currentImage].editorState.imgHeight = imgHeight;
        // this.props.saveAppState( state );

        let newState = {
            imageZoom: imageZoom,
            sliderPositionTB: image.editorState.sliderPositionTB + T,
            sliderPositionLR: image.editorState.sliderPositionLR + L,
            imgWidth: imgWidth,
            imgHeight: imgHeight,
            needUpdateCanvas: true,
        };

        this.props.appImageChanged( this.props.app.currentImage, newState);

    };

    sliderChangeTB = (event, value)=>{this.props.appImageChanged( this.props.app.currentImage, { sliderPositionTB: value, }) };

    sliderChangeLR = (event, value)=>{this.props.appImageChanged( this.props.app.currentImage, { sliderPositionLR: value, }) };

    imageRotate = ()=> {
        let imageRotate = 0;
        switch (this.props.app.images[this.props.app.currentImage].editorState.imageRotate) {
            case 90:
                imageRotate = 180;
                break;
            case 180:
                imageRotate = 270;
                break;
            case 270:
                imageRotate = 0;
                break;
            default:
                imageRotate = 90;
                break;
        }
        this.props.appImageChanged( this.props.app.currentImage, { imageRotate: imageRotate });
    };

    render() {
        // console.log('render ImageEditorZoom, imgWidth:', this.props.parentState.imgWidth);
        const image = this.props.app.images[this.props.app.currentImage];

        return (
            <div className="h-100 d-flex flex-row">
                <div className="app-main-editor-visual-left h-90 flex-grow-0 flex-shrink-0">
                    <div className="vert-slider-button-wrapper text-center d-inline-block">
                        <Fab color="primary" className={'_fabmini ml-3'}
                             onClick={()=>{this.sliderZoom(null, image.editorState.imageZoom*100+0.51)}}
                             disabled={false}
                             aria-label="Colors">
                            <Icon>zoom_in</Icon>
                        </Fab>
                    </div>
                    <div className="vert-slider-wrapper h-85 position-relative d-inline-block">

                        <Slider className='app-slider-for-zoom'
                                classes={{thumb: '_thumb'}}
                                thumb={<Icon className={'app-slider-thumb-icon'} color='primary'>zoom_in</Icon>}
                                vertical
                                max={image.editorState.imageZoomMax*100}
                                min={image.editorState.imageZoomMin*100}
                                value={image.editorState.imageZoom*100}
                                onChange={this.sliderZoom} />
                        {this.props.app.isDebugShow && (
                        <div className={'_value'}><small>{image.editorState.imageZoom}</small></div>
                        )}

                    </div>
                    <div className="vert-slider-button-wrapper text-center d-inline-block">
                        <Fab color="primary" className={'_fabmini ml-3'}
                             onClick={()=>{this.sliderZoom(null, image.editorState.imageZoom*100-0.51)}}
                             disabled={false}
                             aria-label="Colors">
                            <Icon>zoom_out</Icon>
                        </Fab>
                    </div>
                </div>
                <div className="app-main-editor-visual-container h-100  flex-grow-1 flex-shrink-1">
                    <div className="h-90">
                        <ImageEditorZoomCanvas
                            {...this.props}
                        />
                    </div>
                    <div className="app-main-editor-visual-bottom h-10 w-100  d-flex flex-row">
                        <div className="hori-slider-button-wrapper text-center flex-grow-0">
                            <Fab color="primary" className="_fabmini mt-3"
                                 onClick={()=>{this.sliderChangeLR(null, image.editorState.sliderPositionLR-1)}}
                                 disabled={false}
                                 aria-label="Colors">
                                <Icon>navigate_before</Icon>
                            </Fab>
                        </div>
                        <div className=" flex-grow-1">
                            <div className="hori-slider-wrapper h-100 w-100 position-relative d-inline-flex">
                                <Slider className='app-slider-for-position'
                                        classes={{trackAfter: '_trackAfter', thumb: '_thumb'}}
                                        thumb={<Icon className={'app-slider-thumb-icon'} color='primary'>image</Icon>}
                                        max={(this.props.app.editorWidth-image.editorState.maskLR*2)*0.9}
                                        value={image.editorState.sliderPositionLR}
                                        min={(image.editorState.imgWidth)*-0.9}
                                        onChange={this.sliderChangeLR}
                                />

                                {this.props.app.isDebugShow && (
                                    <div className={'_value'}><small>({image.editorState.sliderPositionLR})</small></div>
                                )}
                            </div>

                        </div>
                        <div className="hori-slider-button-wrapper text-center flex-grow-0" >
                            <Fab color="primary" className={'_fabmini mt-3'}
                                 onClick={()=>{this.sliderChangeLR(null, image.editorState.sliderPositionLR+1)}}
                                 disabled={false}
                                 aria-label="Colors">
                                <Icon>navigate_next</Icon>
                            </Fab>
                        </div>
                    </div>
                </div>
                <div className="app-main-editor-visual-right h-90  flex-grow-0 flex-shrink-0">

                    <div className="d-inline-flex flex-column h-100">
                        <div className="vert-slider-button-wrapper text-center d-inline-block">
                            <Fab color="primary" className={'_fabmini'}
                                 onClick={()=>{this.sliderChangeTB(null, image.editorState.sliderPositionTB-1)}}
                                 disabled={false}
                                 aria-label="Colors">
                                <Icon>expand_less</Icon>
                            </Fab>
                        </div>
                        <div className="vert-slider-wrapper h-85 position-relative d-inline-flex">
                            <Slider className='app-slider-for-position'
                                    classes={{trackAfter: '_trackAfter', thumb: '_thumb'}}
                                    thumb={<Icon className={'app-slider-thumb-icon'} color='primary'>image</Icon>}
                                    max={(image.editorState.imgHeight)*-0.9}
                                    value={image.editorState.sliderPositionTB}
                                    min={(this.props.app.editorHeight-image.editorState.maskTB*2)*0.9}
                                    onChange={this.sliderChangeTB}
                                    vertical
                            />
                            {this.props.app.isDebugShow && (
                                <div className={'_value'}><small>({image.editorState.sliderPositionTB})</small></div>
                            )}
                        </div>
                        <div className="vert-slider-button-wrapper text-center d-inline-block">
                            <Fab color="primary" className={'_fabmini'}
                                 onClick={()=>{this.sliderChangeTB(null, image.editorState.sliderPositionTB+1)}}
                                 disabled={false}
                                 aria-label="Colors">
                                <Icon>expand_more</Icon>
                            </Fab>
                        </div>

                    </div>


                    <div className="vert-buttons-wrapper h-100 d-inline-flex">
                        <div className="app-main-editor-visual-right-buttons">
                            <div className="zoom-positioning">
                                <div className="zoom-positioning-w1 zoom-positioning-h1  d-inline-block"/>
                                <div className="zoom-positioning-w2 zoom-positioning-h1 d-inline-block">
                                    <button type="button"
                                            className={"btn btn-primary h-100"}
                                            onClick={()=>{this.props.alignTop()}}>
                                        <Icon>vertical_align_top</Icon>
                                    </button>
                                </div>
                                <div className="zoom-positioning-w1 zoom-positioning-h1 d-inline-block"/>
                            </div>
                            <div className="zoom-positioning">
                                <div className="zoom-positioning-w1 zoom-positioning-h2 d-inline-block">
                                    <button type="button"
                                            className={"btn btn-primary h-100"}
                                            onClick={()=>{this.props.alignLeft()}}>
                                        <Icon style={{transform:'rotate(270deg)'}}>vertical_align_top</Icon>
                                    </button>
                                </div>
                                <div className="d-inline-block">
                                    <div className="zoom-positioning-w2 zoom-positioning-h1">
                                        <button type="button"
                                                className={"btn btn-primary h-100"}
                                                onClick={()=>{this.props.alignLandscape(this.props.app.currentImage, image.format)}}>
                                            <Icon>swap_horiz</Icon>
                                        </button>
                                    </div>
                                    <div className="zoom-positioning-w2 zoom-positioning-h1">
                                        <button type="button"
                                                className={"btn btn-primary h-100"}
                                                onClick={()=>{this.props.alignPortrait(this.props.app.currentImage, image.format)}}>
                                            <Icon>swap_vert</Icon>
                                        </button>
                                    </div>
                                </div>
                                <div className="zoom-positioning-w1 zoom-positioning-h2 d-inline-block">
                                    <button type="button"
                                            className={"btn btn-primary h-100"}
                                            onClick={()=>{this.props.alignRight()}}>
                                        <Icon style={{transform:'rotate(90deg)'}}>vertical_align_top</Icon>
                                    </button>
                                </div>
                            </div>
                            <div className="zoom-positioning">
                                <div className="zoom-positioning-w1 zoom-positioning-h1 d-inline-block"/>
                                <div className="zoom-positioning-w2 zoom-positioning-h1 d-inline-block">
                                    <button type="button"
                                            className={"btn btn-primary h-100"}
                                            onClick={()=>{this.props.alignBottom()}}>
                                        <Icon style={{transform:'rotate(180deg)'}}>vertical_align_top</Icon>
                                    </button>
                                </div>
                                <div className="zoom-positioning-w1 zoom-positioning-h1 d-inline-block"/>
                            </div>
                            <div className="app-main-editor-visual-rotate-buttons mt-4 text-center">
                                <button type="button"
                                        className={"btn  btn-big btn-primary"}
                                        onClick={this.imageRotate}>
                                    <Icon>rotate_right</Icon>
                                </button>
                            </div>


                        </div>

                    </div>

                </div>
            </div>

        )
    }
}
