import React from 'react';
import * as tools from "../../tools";
import axios from "axios";

import './index.css';
import Icon from "@material-ui/core/Icon";
import Paper from '@material-ui/core/Paper';
import {makeGetUri} from "../../tools";
import Switch from "@material-ui/core/Switch";
import Fab from "@material-ui/core/Fab";
import t from "../i18n";


export default class PageSetupFormats extends React.Component {

    state = {
        editedFormats: null,
    };

    componentDidMount() {
        let formats = this.props.app.formats.slice();
        for(let i=0; i<formats.len; i++) {
            formats[i].cost = tools.Round(formats[i].cost)
        }
        this.setState( {editedFormats: formats} );

    }

    componentDidUpdate(prevProps, prevState, snapshot) {
    }

    formatNew = ()=>{
        let formats = this.state.editedFormats.slice();
        if(formats.length>0) {
            formats.push( Object.assign({}, formats[formats.length-1]) );
        } else {
            formats.push( {} );
        }
        const last = formats.length-1;
        formats[last].name = 'new format';
        formats[last].enabled = false;

        this.setState({editedFormats: formats});

    };

    formatRemove = (index) => {
        let formats = this.state.editedFormats;
        formats.splice(index,1);
        this.setState({editedFormats: formats});

    };
    formatChange = (index, id, ev) => {
        let formats = this.state.editedFormats;
        let changed = true;
        switch (id) {
            case 'enabled':
            case 'portrait':
                formats[index][id] = !formats[index][id];
                break;

            case 'name':
            case 'height':
            case 'width':
            case 'surface':
                formats[index][id] = ev.target.value;
                break;

            case 'dpi':
                formats[index][id] = tools.Round(ev.target.value,0);
                break;

            case 'cost':
                const x = ev.target.value.replace(/,/g,'.');
                formats[index][id] = x;
                break;

            default:
                changed = false;

        }
        if(changed)
            this.setState({editedFormats: formats});
    };

    formatSave() {
        let formats = this.state.editedFormats.slice();
        console.log(formats);
        for(let i=0; i<formats.length; i++) {
            // console.log(i, formats[i]);

            formats[i].dpi = Math.max(150, Math.min(tools.Round(formats[i].dpi,0), 1500));
            formats[i].cost = tools.Round(formats[i].cost,2);
        }
        let uri = makeGetUri();
        const params = new URLSearchParams();
        params.append('q', 'setupFormatSave');
        params.append('formats', JSON.stringify(formats));
        params.append('p', this.props.app.isSetupPassword);
        axios.post(uri, params ).then(res => {
            if(res.status===200) {
                if(res.data.error==='') {
                    this.props.appSetState({formats: formats}, ()=>{
                        this.props.onMessage( t('successful saved') );
                    });

                } else {
                    this.props.onMessage('error :'+res.data.error);
                }
            } else {
                console.log('axios.post status ', res.status);
            }
        });
    }

    formatMove(index,shift) {
        let newIndex = index+shift;

        console.log(newIndex);
        if(newIndex<0 || newIndex>this.state.editedFormats.length-1 ) {
            return false;
        }
        let formats = this.state.editedFormats.slice();
        let tmp = formats[index];
        formats[index] = formats[newIndex];
        formats[newIndex] = tmp;
        this.setState({editedFormats: formats});

    }

    render() {

        let formats = (this.state.editedFormats ? this.state.editedFormats : []);

        return (
            <div className="app-page app-bg-e0 app-page-setup-formats w-100 h-100">
                <div className="app-setup  h-100">



                    <div className="app-setup-main h-100">
                        <Paper className="app-setup-formats scrollbar p-2 h-100">
                            <div className="app-setup-formats-header">
                                <div className="_wrp _wrp_enabled"><small>On</small></div>
                                <div className="_wrp _wrp_name"><small>{t('Name')}</small></div>
                                <div className="_wrp _wrp_dpi"><small>DPI</small></div>
                                <div className="_wrp _wrp_height"><small>{t('Height')}</small></div>
                                <div className="_wrp _wrp_width"><small>{t('Width')}</small></div>
                                <div className="_wrp _wrp_surface"><small>{t('Surface')}</small></div>
                                <div className="_wrp _wrp_cost"><small>{t('Cost')}</small></div>
                                <div className="_wrp _wrp_portrait"><small>{t('Portrait')}</small></div>
                                <div className="_wrp _wrp_movecontrol"> </div>
                            </div>

                            {formats.map((format, index)=>
                                <div key={index} className={"app-setup-format-item"+(format.enabled ? '' : ' _disabled' )}>
                                    <div className="_wrp _wrp_enabled">
                                        <Switch
                                            checked={format.enabled}
                                            onChange={(ev) => this.formatChange(index,'enabled', ev)}
                                            value={'enabled'}
                                            color="primary"
                                        />

                                    </div>
                                    <div className="_wrp _wrp_name">
                                        <input type="text" value={format.name} onChange={(ev) => this.formatChange(index,'name', ev)}/>
                                    </div>
                                    <div className="_wrp _wrp_dpi">
                                        <input type="text" value={format.dpi} onChange={(ev) => this.formatChange(index,'dpi', ev)}/>
                                    </div>
                                    <div className="_wrp _wrp_height">
                                        <select value={format.height} data-hint={format.height} onChange={(ev) => this.formatChange(index,'height', ev)}>
                                            {[2.5, 3.5, 4, 4.5, 5, 6, 8, 10, 11, 12].map((h, i)=>
                                                <option key={i} value={h}>{h}" ({Math.round(h*25.4)})</option>
                                            )}
                                        </select>

                                    </div>
                                    <div className="_wrp _wrp_width">
                                        <select value={format.width} onChange={(ev) => this.formatChange(index,'width', ev)}>
                                            {[3.5, 5, 6, 7, 8, 10, 12, 14, 15, 16, 17, 18].map((h, i)=>
                                                <option key={i} value={h}>{h}" ({Math.round(h*25.4)})</option>
                                            )}
                                        </select>
                                    </div>
                                    <div className="_wrp _wrp_surface">
                                        <select value={format.surface} onChange={(ev) => this.formatChange(index,'surface', ev)}>
                                            {['glance', 'matte'].map((h, i)=>
                                                <option key={i} value={h}>{t(h)}</option>
                                            )}
                                        </select>
                                    </div>
                                    <div className="_wrp _wrp_cost">
                                        <input type="text" value={format.cost} onChange={(ev) => this.formatChange(index,'cost', ev)}/>
                                    </div>
                                    <div className="_wrp _wrp_portrait">
                                        <Switch
                                            checked={format.portrait}
                                            onChange={(ev) => this.formatChange(index,'portrait', ev)}
                                            value={'portrait'}
                                            color="primary"
                                        />
                                    </div>

                                    {format.enabled && (
                                        <div className="_wrp _wrp_movecontrol">
                                            <div>
                                                <Icon className="text-primary"
                                                      onClick={()=>{this.formatMove(index,-1)}}
                                                >arrow_upward</Icon>
                                            </div>
                                            <div>
                                                <Icon className="text-primary"
                                                      onClick={()=>{this.formatMove(index,1)}}
                                                >arrow_downward</Icon>
                                            </div>
                                        </div>
                                    )}
                                    {!format.enabled && (
                                        <div className="_wrp _wrp_movecontrol">
                                            <Fab className={"app-setup-format-item-remove"}
                                                 onClick={() => this.formatRemove(index)}
                                                 color="secondary" aria-label="Remove"
                                                 size="small">
                                                <Icon>delete</Icon>
                                            </Fab>
                                        </div>

                                    )}




                                    <hr/>
                                </div>
                            )
                            }
                            <div className="app-setup-format-new">
                                <button className={"btn btn-primary"}
                                        onClick={this.formatNew}
                                        disabled={false}
                                        title="newFormat"
                                        type="button" >
                                    <Icon>add</Icon>
                                </button>
                                <span className="float-right">
                                    <button className={"btn btn-success "}
                                            onClick={this.formatSave.bind(this)}
                                            disabled={false}
                                            title="Save"
                                            type="button" >
                                        <Icon>save</Icon>
                                    </button>

                                </span>
                            </div>

                        </Paper>
                    </div>


                </div>
            </div>
        )
    }
}

