import React from 'react';
import './splash-screen.css';
import t from "./i18n";
import {getLangs} from "./i18n";
import {setLang} from "./i18n";
import '../index.css';
import Icon from "@material-ui/core/Icon";


export default class LoaderSplashScreen extends React.Component {
    state = {
        caseInfinityLoop: false,
    };

    componentDidMount() {
        this.tm = setTimeout( ()=>{ this.setState({caseInfinityLoop:true}) }, 179000);
    }
    componentWillUnmount() {
        if(this.tm) clearTimeout(this.tm);
    }

    langOnLoad(key) {
        setLang(key);
        this.setState({caseInfinityLoop:false});
    }

    render() {

        let langs = getLangs();

        return (
            <div className="splash-screen app-bg-e">
                {this.props.serverError || this.state.caseInfinityLoop ?
                    <div className='text-center'>
                        <div className="splash-screen-text-wrapper error bg-danger text-light pr-5 pl-5">
                            {t('Server error:')}
                            <br/><br/>
                            {this.props.serverError}
                        </div>
                        <div className="p-4">
                            <button className={"btn btn-primary btn-big btn-3d"}
                                    onClick={()=>{ document.location.reload();}}
                                    disabled={false}
                                    title="Refresh"
                                    type="button" >
                                <Icon>update</Icon> {t('Restart')}
                            </button>
                        </div>

                    </div>
                    :
                    <div className="splash-screen-text-wrapper">
                        {t('Wait a moment while app loading')}
                        <div className="loading-dot">.</div>
                    </div>
                }

                <div className="splash-screen-lang-buttons">
                    {langs.map( (lang, index)=>
                        <div key={index} className="m-3 d-inline-block">
                            <button className="btn btn-success btn-3d p-3"
                                    onClick={()=>this.langOnLoad(lang.key)}
                                    disabled={false}
                                    title="Language"
                                    type="button" >
                                <Icon>language</Icon>{lang.caption}
                            </button>

                        </div>
                    )}

                </div>


            </div>
        )

    }
}

